import logger from 'redux-logger';
import thunkMiddleware from 'redux-thunk';
import { PayloadAction } from '@reduxjs/toolkit';
import mapper from 'src/mappers';

const apiResponseMapperMiddleware = (store: any) => (next: CallableFunction) => (
  action: PayloadAction<any>
) => {
  const result = next(mapper(action));
  return result;
};

export { thunkMiddleware, apiResponseMapperMiddleware, logger };
