import React from 'react';
import { Grid, Responsive, Dropdown } from 'semantic-ui-react';
import { logout } from 'src/@slices/auth/logout';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import Avatar from 'src/components/avatar';
import { UserModel } from 'src/models/user';

type Props = {
  user: UserModel
};

const UserMenu = ({ user }: Props) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { displayName, photoUrl } = user || {};

  return (
    <Grid className="user-menu">
      <Grid.Row columns="2">
        <Grid.Column
          verticalAlign="middle"
          textAlign="center"
          className="user-avatar"
          width={5}
        >
          <Avatar width={35} className="margin-auto" content={photoUrl || displayName} />
          <Dropdown floating direction="left">
            <Dropdown.Menu>
              <Dropdown.Item
                onClick={() => history.push('/favorites')}
                icon="heart"
                text="My Favorites"
              />
              <Dropdown.Item
                onClick={() => history.push('/account')}
                icon="user"
                text="My Account"
              />
              <Dropdown.Item
                onClick={() => dispatch(logout())}
                icon="sign-out"
                text="Logout"
              />
            </Dropdown.Menu>
          </Dropdown>
        </Grid.Column>
        <Responsive
          as={Grid.Column}
          minWidth={650}
          textAlign="left"
          className="user-info word-limit"
          verticalAlign="middle"
          width={11}
        >
          {displayName}
        </Responsive>
      </Grid.Row>
    </Grid>
  );
};

export default UserMenu;
