import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AppDispatch } from 'src/@store';
import Firebase from 'src/services/firebase';

type LogoutState = {
  pending: boolean;
  success?: boolean;
  error?: string;
};

const initialState = {
  pending: false,
} as LogoutState;

const logoutSlice = createSlice({
  name: 'logout',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled() {
      return initialState;
    },
    rejected(state, action: PayloadAction<Error>) {
      state.pending = false;
    },
  },
});

export const logout = () => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    await Firebase.logout();
    dispatch(fulfilled());
  } catch (e) {
    dispatch(rejected(e));
  }
};

const { actions, reducer } = logoutSlice;

export const { pending, fulfilled, rejected } = actions;
export default reducer;
