import React, { useEffect } from 'react';
import Playlist from 'src/components/playlist';
import { useDispatch, useSelector } from 'react-redux';
import {
  fetchRecentlyAddedMixes,
  normalizeRecentlyAddedMixes,
} from 'src/@creators/mixes';
import * as indicator from 'src/@creators/screenIndicator';
import { RECENTLY_ADDED_MIXES } from 'src/constants/screenIndicator';
import { RootState } from 'src/@store';
import { dock } from 'src/utils/functors';
import { Header } from 'semantic-ui-react';
import { addSeries, mapGenres, addArtist } from 'src/utils/relations';

const RecentlyAdded = () => {
  const dispatch = useDispatch();
  const state = useSelector<RootState>((state) => state) as RootState;

  const {
    series: { series, loaded: seriesLoaded },
    genres: { genres, loaded: genresLoaded },
    artists: { artists, loaded: artistsLoaded },
    recentlyAddedMixes: { mixes, loaded, normalized },
  } = state;

  useEffect(() => {
    indicator.start(RECENTLY_ADDED_MIXES);
    !loaded && dispatch(fetchRecentlyAddedMixes());
  }, [loaded, dispatch]);

  useEffect(() => {
    if (!normalized && seriesLoaded && genresLoaded && artistsLoaded) {
      const newMixes = dock(mixes)
        .add(addSeries(series))
        .add(addArtist(artists))
        .map(mapGenres(genres))
        .get();
      dispatch(normalizeRecentlyAddedMixes(newMixes));
    }
    normalized && indicator.end(RECENTLY_ADDED_MIXES);
  }, [
    mixes,
    series,
    genres,
    artists,
    dispatch,
    normalized,
    seriesLoaded,
    artistsLoaded,
    genresLoaded,
  ]);

  return loaded ? (
    <>
      <Header className="popular-title" as="h1">
        Recently Added
      </Header>
      <Playlist items={mixes} />
    </>
  ) : null;
};

export default RecentlyAdded;
