import React, { useContext } from 'react';
import { Menu, Input, Icon } from 'semantic-ui-react';
import { AuthContext, AuthContextProps } from 'src/providers/AuthProvider';
import { useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import UserMenu from './UserMenu';

const WideScreen = () => {
  const { user } = useSelector((state: RootState) => state.auth);
  const { openLoginModal } = useContext(AuthContext) as AuthContextProps;
  return (
    <Menu pointing size="large" className="wide-navigation">
      <Menu.Menu position="left">
        <Menu.Item>
          <Input icon="search" iconPosition="left" placeholder="Search music..." />
        </Menu.Item>
      </Menu.Menu>
      <Menu.Menu position="right">
        <Menu.Item>
          {!user && (
            <>
              <Icon name="sign in alternate" />
              <a onClick={() => openLoginModal()} href="#/">
                Login
              </a>
              <Icon name="ellipsis vertical" />
            </>
          )}
          {user && <UserMenu user={user} />}
        </Menu.Item>
      </Menu.Menu>
    </Menu>
  );
};

export default WideScreen;
