import React from 'react';
import { Mix } from 'src/models/mix';

type Props = {
  mix: Mix;
};

const Genres = ({ mix }: Props) => {
  const { genres = [] } = mix;
  const labeledGenres = genres.filter(Boolean).map(({ name }) => name).join(', ');
  return <div className="genres-wrapper word-limit">{labeledGenres}</div>;
};

export default Genres;
