import Firebase from 'src/services/firebase';
import { AppDispatch } from 'src/@store';
import {
  pending as recentlyAddedMixesPending,
  rejected as recentlyAddedMixesRejected,
  fulfilled as recentlyAddedMixesFulfilled,
  normalized as recentlyAddedMixesNormalized,
} from '../@slices/mix/recentlyAddedMixes';
import { toData, queryBuilder } from 'src/utils/firebase';
import MixModel, { Mix } from 'src/models/mix';
import { QueryClauses } from 'src/utils/types';

export const fetchMixes = (
  clauses: QueryClauses,
  pending: CallableFunction,
  fulfilled: CallableFunction,
  rejected: CallableFunction
) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    let collection = Firebase.db.collection('mixes');
    const query = await queryBuilder(collection, clauses);
    const { docs = [] } = await query.get();
    const data = toData<Mix>(docs);
    dispatch(fulfilled(data));
  } catch (e) {
    dispatch(rejected(e));
  }
};

export const fetchRecentlyAddedMixes = (limit: number = 5) => async (
  dispatch: AppDispatch
) => {
  try {
    dispatch(recentlyAddedMixesPending());
    const mixes = Firebase.db
      .collection('mixes')
      .orderBy('createdAt', 'desc')
      .limit(limit);

    const { docs = [] } = await mixes.get();
    const data = toData<Mix>(docs);
    dispatch(recentlyAddedMixesFulfilled(data));
  } catch ({ message }) {
    dispatch(recentlyAddedMixesRejected(message));
  }
};

export const normalizeRecentlyAddedMixes = (mixes: MixModel[]) => async (
  dispatch: AppDispatch
) => {
  dispatch(recentlyAddedMixesNormalized(mixes));
};
