import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Series } from 'src/models/series';

export type SeriesState = {
  pending: boolean;
  loaded: boolean;
  error: string | null;
  series: Array<Series>;
};

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  series: [],
} as SeriesState;

const seriesSlice = createSlice({
  name: 'series',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled(state, action: PayloadAction<Series[]>) {
      state.series = action.payload;
      state.pending = false;
      state.loaded = true;
    },
    rejected(state, action: PayloadAction<any>) {
      state.error = action.payload;
      state.pending = false;
    },
  },
});

const { actions, reducer } = seriesSlice;

export const { pending, fulfilled, rejected } = actions;
export default reducer;
