import React from 'react';
import { Menu, Icon } from 'semantic-ui-react';
import { Genre } from 'src/models/genre';
import { NavLink } from 'react-router-dom';

type Props = {
  genres: Genre[];
};

const GenresPartial = ({ genres }: Props) => {
  return (
    <>
      <Menu.Item as="span" className="title">
        Genres
      </Menu.Item>
      {genres.slice(0, 4).map(({ name, slug }, index) => (
        <Menu.Item as={NavLink} key={index} to={`/genres/${slug}`}>
          <Icon name="tag" size="tiny" />
          {name}
        </Menu.Item>
      ))}
    </>
  );
};

export default GenresPartial;
