import Model from './model';
import { KeyValueObject } from 'src/utils/types';

export type Artist = {
  id?: string;
  name: string;
  slug: string;
  image: string;
  genres: string[];
  lastMixTitle?: string;
  lastMixCreatedAt?: { seconds: number; nanoseconds: number };
};

export default class ArtistModel extends Model implements Artist {
  public id: string;
  public name: string;
  public slug: string;
  public image: string;
  public genres: string[];
  public lastMixTitle: string;
  public lastMixCreatedAt: { seconds: number; nanoseconds: number };

  constructor({
    id,
    name,
    slug,
    image,
    genres,
    lastMixCreatedAt,
    lastMixTitle,
  }: KeyValueObject) {
    super();
    this.id = id;
    this.name = name;
    this.slug = slug;
    this.image = image;
    this.genres = genres;
    this.lastMixTitle = lastMixTitle;
    this.lastMixCreatedAt = { ...lastMixCreatedAt };
  }
}
