import Firebase from 'src/services/firebase';
import { AppDispatch } from 'src/@store';
import { pending, rejected, fulfilled } from '../@slices/series/series';
import { toData } from 'src/utils/firebase';
import { Series } from 'src/models/series';

export const fetchSeries = () => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const series = Firebase.db.collection('series');
    const { docs = [] } = await series.get();
    dispatch(fulfilled(toData<Series>(docs)));
  } catch ({ message }) {
    dispatch(rejected(message));
  }
};
