import React, { useEffect, useState } from 'react';
import { Container } from 'semantic-ui-react';
import Gutter from 'src/theme/gutter';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'src/@store';
import { useParams } from 'react-router-dom';
import PageGuard from 'src/components/pageGuard';
import PageHeader from 'src/components/@partials/pageHeader';
import { fetchMixes } from 'src/@creators/mixes';
import {
  pending,
  fulfilled,
  fulfilledAppend,
  rejected,
} from 'src/@slices/series/seriesDetail';
import { Mix } from 'src/models/mix';
import Playlist from 'src/components/playlist';
import { addSeries, mapGenres, addArtist } from 'src/utils/relations';
import { dock } from 'src/utils/functors';
import { KeyValueObject } from 'src/utils/types';
import $APP from 'src/configs/app';

const { PAGINATION } = $APP;

const query = {
  orderBy: ['count', 'desc'],
  limit: PAGINATION.limit,
} as KeyValueObject;

const SeriesDetailPage = () => {
  const { slug: uriSlug } = useParams<{slug: string}>();
  const [playlistMixes, setPlaylistMixes] = useState([]);
  const dispatch = useDispatch();
  const {
    genres: { genres },
    series: { series: seriesList, loaded: seriesLoaded },
    artists: { artists },
    seriesDetail: { mixes, lastPartCount, loaded: mixesLoaded },
  } = useSelector<RootState, RootState>((state) => state);
  const series = seriesList.find(({ slug }) => slug === uriSlug);

  const fetch = (id: string, lastItemId?: string) => {
    dispatch(
      fetchMixes(
        {
          where: [
            ['status', '==', 1],
            ['seriesId', '==', id],
          ],
          ...query,
          startAt: lastItemId,
        },
        pending,
        fulfilledAppend,
        rejected
      )
    );
  };
  useEffect(() => {
    if (seriesLoaded && series) {
      const { id } = series;
      dispatch(
        fetchMixes(
          {
            where: [
              ['status', '==', 1],
              ['seriesId', '==', id],
            ],
            ...query,
          },
          pending,
          fulfilled,
          rejected
        )
      );
    }
  }, [seriesLoaded, series, dispatch]);

  useEffect(() => {
    if (mixesLoaded) {
      const series = dock(seriesList).map(mapGenres(genres)).get();
      const newMixes = dock(mixes)
        .add(addSeries(series))
        .add(addArtist(artists))
        .map(mapGenres(genres))
        .get();
      setPlaylistMixes(newMixes);
    }
  }, [mixes, mixesLoaded, seriesList, dispatch, genres, artists]);

  const { id: seriesId, name, image } = series || {};
  return (
    <PageGuard cond={seriesLoaded ? !!series : true}>
      <Container fluid className="seriesPage reset">
        <PageHeader title={name} image={image} />
        <Gutter
          top={3}
          right={1.5}
          bottom={2}
          left={2}
          style={{ overflowY: 'auto', height: 'calc(100vh - 200px)' }}
        >
          <Playlist
            items={playlistMixes}
            onLoad={
              lastPartCount === query.limit
                ? ({ id }: Mix) => seriesId && fetch(seriesId, id)
                : undefined
            }
          />
        </Gutter>
      </Container>
    </PageGuard>
  );
};

export default SeriesDetailPage;
