import React, { useState, useRef, useEffect } from 'react';
import usePlayer from 'src/hooks/usePlayer';
import TimelineCounter from 'src/components/timelineCounter';
import { calcUnit, ICalcUnit } from 'src/utils/functors';
import { Responsive } from 'semantic-ui-react';
import usePrevious from 'src/hooks/usePrevious';

const MiniTimeline = () => {
  const {
    service: { audio },
    currentMix,
    isPlaying,
    hasError,
    saveSeconds,
    getSeconds,
  } = usePlayer();

  const { currentTime } = audio;
  const [startTime, setStartTime] = useState(currentTime || getSeconds());
  const [x, setX] = useState(0);
  const containerRef = useRef<HTMLDivElement>(null);
  const durationRef = useRef<number>(currentMix?.duration || 0);
  const initialSecondsRef = useRef<number>(getSeconds());
  const prevMix = usePrevious(currentMix);

  const onUpdate = () => {
    const el = containerRef.current || document.getElementById('mini-tl-wrapper');
    el && setStartTime(audio.currentTime);
  };

  const onCount = (seconds: number) => {
    const containerWidth = containerRef?.current?.clientWidth || 0;
    setX(
      calcUnit({ left: seconds, right: durationRef.current })
        .map(({ toPercent }: ICalcUnit) => {
          return calcUnit({ left: containerWidth, right: toPercent() });
        })
        .toNumber()
    );
    saveSeconds(seconds);
  };

  // when current mix changed
  useEffect(() => {
    setX(0);
    setStartTime(0);
    durationRef.current = currentMix?.duration || 0;
  }, [currentMix]);

  // when any exception throwed
  useEffect(() => {
    if (hasError) {
      setX(0);
      setStartTime(0);
    }
  }, [hasError]);

  // set initial time
  useEffect(() => {
    // Added prev mix check to make sure to run this block on time
    // Because if has mix played in the history, it must be came to set initial time.
    // If prevMix variable is truthy, do not need to run this block.
    if (prevMix) return;

    const duration = durationRef.current;
    const seconds = initialSecondsRef.current;
    const containerWidth = containerRef?.current?.clientWidth || 0;
    setX(
      calcUnit({ left: seconds, right: duration })
        .map(({ toPercent }: ICalcUnit) => {
          return calcUnit({ left: containerWidth, right: toPercent() });
        })
        .toNumber()
    );
    setStartTime(seconds);
  }, [prevMix]);

  return (
    <div ref={containerRef} id="mini-tl-wrapper">
      <Responsive
        fireOnMount
        className="mini-content"
        as="div"
        width={12}
        onUpdate={onUpdate}
      >
        <div style={{ width: `${x}px` }} />
        <TimelineCounter
          visible={false}
          current={startTime}
          duration={durationRef.current}
          active={isPlaying}
          onCount={onCount}
        />
      </Responsive>
    </div>
  );
};

export default MiniTimeline;
