import React from 'react';
import { Grid, Card } from 'semantic-ui-react';
import { Genre } from 'src/models/genre';
import { useHistory } from 'react-router-dom';

type GenresProps = {
  items?: Genre[];
};

const Genres = ({ items = [] }: GenresProps) => {
  const history = useHistory();
  return (
    <Grid stackable className="genres-grid">
      <Grid.Row columns={4}>
        {items.map(({ slug, name, id }) => (
          <Grid.Column width={8} key={id}>
            <Card onClick={() => history.push(`/genres/${slug}`)} header={name} />
          </Grid.Column>
        ))}
      </Grid.Row>
    </Grid>
  );
};

export default Genres;
