import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Mix } from 'src/models/mix';
import { app } from 'src/utils/config';

export type PlayerHistoryState = {
  history: Mix[];
  pointer: number | null;
  length: number;
};

const { storageKey, lastMixStorageKey } = app('HISTORY');
const history = JSON.parse(sessionStorage.getItem(storageKey) || '[]');
const pointer = parseInt(sessionStorage.getItem(lastMixStorageKey) ?? '');
const { length } = history;
const initialState = {
  history,
  length,
  pointer: !isNaN(pointer) ? pointer : length > 0 ? length - 1 : null,
} as PlayerHistoryState;

const playerHistorySlice = createSlice({
  name: 'playerHistory',
  initialState,
  reducers: {
    push(state, action: PayloadAction<Mix>) {
      const { history, pointer } = state;
      const mix = action.payload;

      // if the mix you want to listen is same with last mix,
      // won't add the mix to history.
      // Back/Next actions won't pass this condition,
      // because history pointer will be set before mix didn't play.
      if (pointer !== null && history[pointer].id === mix.id) {
        return;
      }

      const newHistory = history.slice(0, (pointer ?? 0) + 1);

      state.pointer = newHistory.length;
      state.history = [...newHistory, mix];
      state.length = newHistory.length + 1;
    },
    forward({ history, pointer, length }) {
      const index = pointer !== null ? pointer + 1 : 0;
      return {
        length,
        history,
        pointer: history[index] ? index : pointer,
      };
    },
    back({ history, pointer, length }) {
      const index = pointer !== null ? pointer - 1 : 0;
      return {
        length,
        history,
        pointer: history[index] ? index : pointer,
      };
    },
  },
});

const { actions, reducer } = playerHistorySlice;

export const { push, forward, back } = actions;
export default reducer;
