import Firebase from 'src/services/firebase';
import { AppDispatch } from 'src/@store';
import {
  addFulfilled,
  addPending,
  addRejected,
  removePending,
  removeFulfilled,
  removeRejected,
  pending,
  fulfilled,
  rejected
} from '../@slices/favourites/userFavourites';
import * as toast from './toast';
import { UserExtra } from 'src/utils/types';
import { staticText } from 'src/utils/config';

const { FAVOURITES } = staticText('USER');

export const fetchUserFavourites = (userId: string) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const userRef = Firebase.db.collection('users').doc(userId);
    const { favourites = [] } = (await userRef.get()).data() as UserExtra;
    dispatch(fulfilled(favourites));
  } catch (e) {
    dispatch(rejected(e));
  }
};

export const addToUserFavourites = (userId: string, mixId: string) => async (
  dispatch: AppDispatch
) => {
  try {
    dispatch(addPending());
    const userRef = Firebase.db.collection('users').doc(userId);
    const { favourites = [] } = (await userRef.get()).data() as UserExtra;

    if (!favourites.includes(mixId)) {
      favourites.push(mixId);
    }

    await userRef.update({ favourites });
    dispatch(addFulfilled(mixId));
    toast.success(FAVOURITES.ADDED);
  } catch (e) {
    toast.error(e.message);
    dispatch(addRejected(e));
  }
};

export const removeFromUserFavourites = (userId: string, mixId: string) => async (
  dispatch: AppDispatch
) => {
  try {
    dispatch(removePending());
    const userRef = Firebase.db.collection('users').doc(userId);
    const { favourites = [] } = (await userRef.get()).data() as UserExtra;
    const index = favourites.findIndex((id) => id === mixId);

    if (index !== -1) {
      favourites.splice(index, 1);
    }

    await userRef.update({ favourites });
    dispatch(removeFulfilled(mixId));
    toast.success(FAVOURITES.REMOVED);
  } catch (e) {
    toast.error(e.message);
    dispatch(removeRejected(e));
  }
};
