import * as fb from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';
import $AC from 'src/configs/app';
import { User } from 'src/utils/types';

const { config } = $AC.FIREBASE;

export default class Firebase {
  static init() {
    fb.initializeApp(config);
  }

  static async persistentce() {
    return await fb.auth().setPersistence(fb.auth.Auth.Persistence.LOCAL);
  }

  static async login(email: string, password: string): Promise<fb.auth.UserCredential> {
    return await fb.auth().signInWithEmailAndPassword(email, password);
  }

  static async register(auth: RegisterProps): Promise<fb.auth.UserCredential> {
    const { name, email, password, sendVerifyEmail = false } = auth;
    const result = await fb.auth().createUserWithEmailAndPassword(email, password);
    const user = Firebase.auth().currentUser;

    user &&
      (await user.updateProfile({
        displayName: name,
      }));

    if (user && sendVerifyEmail) {
      user.sendEmailVerification();
    }

    return result;
  }

  static get currentUser(): User {
    return JSON.parse(JSON.stringify(Firebase.auth().currentUser));
  }

  static async logout(): Promise<void> {
    return fb.auth().signOut();
  }

  static auth() {
    return fb.auth();
  }

  static get db(): firebase.firestore.Firestore {
    return fb.firestore();
  }
}

export type RegisterProps = {
  name: string;
  email: string;
  password: string;
  sendVerifyEmail?: boolean;
};
export type FirebaseErrorProps = {
  code: string;
  message: string;
};
