import React, { useContext } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Container } from 'semantic-ui-react';
import Sidebar from './components/sidebar';
import Navigation from './components/navigation';
import Modal from './components/modal';
import Home from './pages/home';
import { AuthContext, AuthContextProps } from './providers/AuthProvider';
import Toast from './components/toast';
import ScreenIndicator from './components/screenIndicator';
import Account from './pages/account';
import Favorites from './pages/favorites';
import GenresDetailPage from './pages/genreDetail';
import SeriesDetailPage from './pages/seriesDetail';
import ArtistDetailPage from './pages/artistDetail';
import MainPlayer from './components/mainPlayer';
import './App.scss';
import usePlayer from './hooks/usePlayer';
import usePlayerHistory from './hooks/usePlayerHistory';

function App() {
  const { PrivateRoute } = useContext(AuthContext) as AuthContextProps;
  const { isVisible } = usePlayer();
  const { length: historyLength } = usePlayerHistory();
  return (
    <Router>
      <Container fluid className="App">
        <Sidebar>
          <>
            <Toast />
            <Navigation />
            <Switch>
              <Route exact path="/">
                <Home />
              </Route>
              <Route exact path="/genres/:slug" component={GenresDetailPage} />
              <Route exact path="/series/:slug" component={SeriesDetailPage} />
              <Route exact path="/artists/:slug" component={ArtistDetailPage} />
              <PrivateRoute path="/account">
                <Account />
              </PrivateRoute>
              <PrivateRoute path="/favorites">
                <Favorites />
              </PrivateRoute>
            </Switch>
          </>
        </Sidebar>
        {isVisible && historyLength && <MainPlayer />}
      </Container>
      <Modal />
      <ScreenIndicator />
    </Router>
  );
}

export default App;
