import React, { useState, useEffect } from 'react';
import { Grid, Header, Form, Button } from 'semantic-ui-react';
import Input from 'src/theme/input';
import { ReactInputEvent } from 'src/utils/types';
import { changeEmail, ChangeEmailState } from 'src/@slices/account/changeEmail';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { staticText } from 'src/utils/config';
import { success as successMessage, error as errorMessage } from 'src/@creators/toast';

type Props = {
  email?: string | null;
};

const { CHANGE_EMAIL } = staticText('ACCOUNT');

const ChangeEmail = ({ email: currentEmail }: Props) => {
  const dispatch = useDispatch();
  const { error, pending, success } = useSelector<RootState, ChangeEmailState>(
    (state) => state.changeEmail
  );
  const [email, setEmail] = useState(currentEmail);
  const onChange = ({ target }: ReactInputEvent) => {
    const { value } = target;
    setEmail(value);
  };
  const change = () => email && dispatch(changeEmail(email));

  useEffect(() => {
    error && errorMessage(error);
    success && successMessage(CHANGE_EMAIL.SUCCESS);
  }, [error, success]);
  return (
    <Grid className="change-email">
      <Grid.Row columns="3">
        <Grid.Column tablet="3" computer="3" largeScreen="4" widescreen="5" />
        <Grid.Column mobile="16" computer="10" tablet="10" largeScreen="8" widescreen="6">
          <Header as="h1">Change Email</Header>
          <Form autoComplete="off">
            <Form.Field>
              <Input
                autoComplete="off"
                name="email"
                size="large"
                fluid
                label="Email:"
                value={email}
                onChange={onChange}
                placeholder="Email"
              />
            </Form.Field>
            <Button
              positive
              size="large"
              onClick={change}
              disabled={pending || currentEmail === email}
              content="SAVE"
            />
          </Form>
        </Grid.Column>
        <Grid.Column tablet="3" computer="3" largeScreen="4" widescreen="5" />
      </Grid.Row>
    </Grid>
  );
};

export default ChangeEmail;
