import React from 'react';
import { Header } from 'semantic-ui-react';
import Genres from 'src/components/genres';
import { useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { GenresState } from 'src/@slices/genre/genres';
import { shuffle } from 'src/utils/generic';

const Mood = () => {
  const { featuredGenres, loaded } = useSelector<RootState, GenresState>((state) => state.genres);
  return loaded ? (
    <>
      <Header className="mood-title" as="h1">
        Mood
      </Header>
      <Genres items={shuffle(featuredGenres.slice(0, 4))} />
    </>
  ) : null;
};

export default Mood;
