import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ScreenIndicator } from 'src/utils/types';

export type ScreenIndicatorState = {
  isEmpty: boolean;
  cases: ScreenIndicator[];
};

const initialState = {
  isEmpty: true,
  cases: [],
} as ScreenIndicatorState;

const screenIndicatorSlice = createSlice({
  name: 'screenIndicator',
  initialState,
  reducers: {
    insert(state, action: PayloadAction<ScreenIndicator>) {
      state.cases.push(action.payload);
      state.isEmpty = false;
    },
    remove(state, action: PayloadAction<string>) {
      const removedName = action.payload;
      const filteredCases = state.cases.filter(({ name }) => name !== removedName);
      state.cases = filteredCases;
      state.isEmpty = !filteredCases.length;
    },
    reset() {
      return initialState;
    },
  },
});

const { actions, reducer } = screenIndicatorSlice;

export const { insert, remove, reset } = actions;
export default reducer;
