export default [
  {
    check: (width: number) => width >= 1200,
    genres: {
      classNames: ['pl-2em', 'genres-card-pb-0'].join(' '),
    },
    playlist: {},
  },
  {
    check: (width: number) => width >= 992 && width < 1200,
    genres: {
      classNames: ['pl-1em', 'genres-card-pb-0'].join(' '),
    },
    playlist: {
      classNames: ['pb-2em'],
    },
  },
  {
    check: (width: number) => width >= 768 && width < 992,
    genres: {
      classNames: ['pl-2em'].join(' '),
    },
    playlist: {},
  },
  {
    check: (width: number) => width < 768,
    genres: {
      classNames: ['pl-1em', 'genres-card-pb-0'].join(' '),
    },
    playlist: {
      classNames: ['pb-2em'],
    },
  },
] as PointProps[];

export const defaultPoint = {
  genres: {
    classNames: '',
  },
  playlist: {
    classNames: '',
  },
};

type PointProps = {
  check: CallableFunction;
  genres: GenresProps;
  playlist: PlaylistProps;
};

type GenresProps = {
  classNames: string;
};

type PlaylistProps = {
  classNames?: string;
};
