import { PayloadAction } from '@reduxjs/toolkit';
import { Try } from 'src/utils/generic';
import { fulfilled as profileFulfilled } from 'src/@slices/account/profile';
import { update as authUpdate } from 'src/@slices/auth/auth';
import { fulfilled as recentlyAddedMixesFulfilled } from 'src/@slices/mix/recentlyAddedMixes';
import { fulfilled as fetchArtistsFulfilled } from 'src/@slices/artist/artists';
import {
  fulfilled as fetchSeriesDetailFulfilled,
  fulfilledAppend as fetchSeriesDetailFulfilledAppend,
} from 'src/@slices/series/seriesDetail';
import {
  fulfilled as fetchGenreDetailFulfilled,
  fulfilledAppend as fetchGenreDetailFulfilledAppend,
} from 'src/@slices/genre/genreDetail';
import {
  fulfilled as fetchArtistDetailFulfilled,
  fulfilledAppend as fetchArtistDetailFulfilledAppend,
} from 'src/@slices/artist/artistDetail';
import {
  fulfilled as fetchProductListFulfilled,
  fulfilledAppend as fetchProductListFulfilledAppend,
} from 'src/@slices/favourites/favouriteList';

import profileMapper from './profileMapper';
import authMapper from './authMapper';
import mixMapper from './mixMapper';
import artistMapper from './artistMapper';

const maps = {
  [profileFulfilled.toString()]: profileMapper,
  [authUpdate.toString()]: authMapper,
  [recentlyAddedMixesFulfilled.toString()]: mixMapper,
  [fetchSeriesDetailFulfilled.toString()]: mixMapper,
  [fetchSeriesDetailFulfilledAppend.toString()]: mixMapper,
  [fetchGenreDetailFulfilled.toString()]: mixMapper,
  [fetchGenreDetailFulfilledAppend.toString()]: mixMapper,
  [fetchArtistDetailFulfilled.toString()]: mixMapper,
  [fetchArtistDetailFulfilledAppend.toString()]: mixMapper,
  [fetchProductListFulfilled.toString()]: mixMapper,
  [fetchProductListFulfilledAppend.toString()]: mixMapper,
  [fetchArtistsFulfilled.toString()]: artistMapper,
};

const mapper = (action: PayloadAction<any>) => {
  const { payload, type } = action;
  const mapperInit = maps[type];
  const right = (model: any) => model;
  const left = () => payload;
  const newPayload = Try(() => mapperInit(payload), right, left);
  return { ...action, payload: newPayload };
};

export default mapper;
