import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Genre } from 'src/models/genre';

export type GenresState = {
  pending: boolean;
  loaded: boolean;
  error: string | null;
  genres: Genre[];
  featuredGenres: Genre[];
};

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  genres: [],
  featuredGenres: [],
} as GenresState;

const genreSlice = createSlice({
  name: 'genres',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled(state, action: PayloadAction<Genre[]>) {
      state.genres = action.payload;
      state.pending = false;
      state.loaded = true;
    },
    featuredFulfilled(state, action: PayloadAction<Genre[]>) {
      state.featuredGenres = action.payload.filter(({ featured }) => featured);
      state.loaded = true;
    },
    rejected(state, action: PayloadAction<any>) {
      state.error = action.payload;
      state.pending = false;
    },
  },
});

const { actions, reducer } = genreSlice;

export const { pending, fulfilled, featuredFulfilled, rejected } = actions;
export default reducer;
