import React, { ReactChild, useState, createContext } from 'react';
import { Sidebar as SuiSidebar, Segment, Menu, Icon, Image } from 'semantic-ui-react';
import { useSelector } from 'react-redux';
import SeriesPartial from './partials/series';
import GenresPartial from './partials/genres';
import OthersPartial from './partials/others';
import { RootState } from 'src/@store';
import logo from './../../assets/images/logo.svg';
import { NavLink } from 'react-router-dom';

type Props = {
  children: ReactChild;
};

export const SidebarContext = createContext({});

const Sidebar = ({ children }: Props) => {
  const [visibility, setVisibility] = useState(window.innerWidth > 992);
  const toggle = () => setVisibility(!visibility);

  const {
    series: { series, loaded: isSeriesLoaded },
    genres: { featuredGenres, loaded: isGenresLoaded },
  } = useSelector<RootState, RootState>((state) => state);

  const isLoaded = isSeriesLoaded && isGenresLoaded;

  return (
    <SidebarContext.Provider value={{ toggle, visibility }}>
      <SuiSidebar.Pushable as={Segment} className="app-sidebar">
        <SuiSidebar
          as={Menu}
          animation="push"
          icon="labeled"
          // onHide={() => setVisible(false)}
          vertical
          visible={visibility}
          width="wide"
        >
          <Menu.Header>
            <Image src={logo} href="/" />
          </Menu.Header>
          {isLoaded && (
            <>
              <Menu.Item as={NavLink} icon to="/" exact name="Home">
                <Icon name="home" size="tiny" />
                Home
              </Menu.Item>
              <SeriesPartial series={series} />
              <GenresPartial genres={featuredGenres} />
              <OthersPartial />
            </>
          )}
        </SuiSidebar>
        <SuiSidebar.Pusher dimmed={false}>
          <Segment basic>{children}</Segment>
        </SuiSidebar.Pusher>
      </SuiSidebar.Pushable>
    </SidebarContext.Provider>
  );
};

export default Sidebar;
