import React from 'react';
import { Grid, Header } from 'semantic-ui-react';
import Image from 'src/theme/image';
import { Props } from '.';
import Lazy from 'src/theme/lazy';
import Loader from 'src/components/loader';

const Mobile = ({ title, image }: Props) => {
  return (
    <>
      <Grid.Row columns="1" verticalAlign="middle" textAlign="center">
        <Grid.Column verticalAlign="middle" textAlign="center">
          {image && (
            <Lazy
              src={image}
              placeholder={
                <Loader size="mini" dimmer={false} active verticalAlign="middle" />
              }
            >
              {(currentSrc: string) => (
                <Image src={currentSrc} size="tiny" verticalAlign="middle" />
              )}
            </Lazy>
          )}
        </Grid.Column>
      </Grid.Row>
      <Grid.Row>
        <Grid.Column textAlign="center">
          <Header size="large" as="h1">
            {title}
          </Header>
        </Grid.Column>
      </Grid.Row>
    </>
  );
};

export default Mobile;
