import React, { useState, useEffect } from 'react';
import { Container } from 'semantic-ui-react';
import PageGuard from 'src/components/pageGuard';
import PageHeader from 'src/components/@partials/pageHeader';
import Gutter from 'src/theme/gutter';
import Playlist from 'src/components/playlist';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'src/@store';
import { KeyValueObject } from 'src/utils/types';
import $APP from 'src/configs/app';
import { useParams } from 'react-router-dom';
import { Mix } from 'src/models/mix';
import { fetchMixes } from 'src/@creators/mixes';
import {
  pending,
  fulfilled,
  fulfilledAppend,
  rejected,
} from 'src/@slices/genre/genreDetail';
import { dock } from 'src/utils/functors';
import { mapGenres, addSeries, addArtist } from 'src/utils/relations';

const { PAGINATION } = $APP;

const query = {
  orderBy: ['createdAt', 'desc'],
  limit: PAGINATION.limit,
} as KeyValueObject;

const GenreDetailPage = () => {
  const { slug: uriSlug } = useParams<{slug: string}>();
  const dispatch = useDispatch();
  const [playlistMixes, setPlaylistMixes] = useState([]);
  const {
    artists: { artists },
    genres: { genres, loaded: genresLoaded },
    series: { series: seriesList, loaded: seriesLoaded },
    genreDetail: { mixes, lastPartCount, loaded: mixesLoaded },
  } = useSelector<RootState, RootState>((state) => state);
  const genre = genres.find(({ slug }) => slug === uriSlug);

  const fetch = (id: string, lastItemId?: string) => {
    dispatch(
      fetchMixes(
        {
          where: [
            ['status', '==', 1],
            ['genres', 'array-contains', id],
          ],
          ...query,
          startAt: lastItemId,
        },
        pending,
        fulfilledAppend,
        rejected
      )
    );
  };
  useEffect(() => {
    if (genresLoaded && genre) {
      const { id } = genre;
      dispatch(
        fetchMixes(
          {
            where: [
              ['status', '==', 1],
              ['genres', 'array-contains', id],
            ],
            ...query,
          },
          pending,
          fulfilled,
          rejected
        )
      );
    }
  }, [genresLoaded, genre, dispatch]);

  useEffect(() => {
    if (mixesLoaded && seriesLoaded) {
      const series = dock(seriesList).map(mapGenres(genres)).get();
      const newMixes = dock(mixes)
        .add(addArtist(artists))
        .add(addSeries(series))
        .map(mapGenres(genres))
        .get();
      setPlaylistMixes(newMixes);
    }
  }, [mixes, mixesLoaded, seriesLoaded, seriesList, dispatch, genres, artists]);

  const { id: genreId, name, image } = genre || {};

  return (
    <PageGuard cond={genresLoaded ? !!genre : true}>
      <Container fluid className="genreDetailPage reset">
        <PageHeader title={name} image={image} />
        <Gutter
          top={3}
          right={1.5}
          bottom={2}
          left={2}
          style={{ overflowY: 'auto', height: 'calc(100vh - 200px)' }}
        >
          <Playlist
            items={playlistMixes}
            onLoad={
              lastPartCount === query.limit
                ? ({ id }: Mix) => genreId && fetch(genreId, id)
                : undefined
            }
          />
        </Gutter>
      </Container>
    </PageGuard>
  );
};

export default GenreDetailPage;
