import React, { useRef } from 'react';
import { useEffect } from 'react';
import secToFormat from 'sec-to-format';

type Props = {
  current: number;
  duration?: number;
  active: boolean;
  elapsed?: boolean;
  remaining?: boolean;
  timeFormat?: string;
  onCount?: CallableFunction;
  visible?: boolean;
};

const TimelineCounter = ({
  remaining = false,
  elapsed = !remaining,
  current,
  duration = 0,
  active,
  timeFormat = '%H:%m:%s',
  onCount,
  visible = true,
}: Props) => {
  const setter = (num: number) => (num < 10 ? `0${num}` : num);
  const setters = {
    '%m': setter,
    '%s': setter,
  };
  const secRef = useRef<number>(current);
  const intervalRef = useRef<number | boolean | null>();

  useEffect(() => {
    const { current: intValIndex } = intervalRef;
    if (!intValIndex && active) {
      intervalRef.current = setInterval(() => {
        secRef.current++;
        const seconds = secRef.current;
        active && onCount && onCount(elapsed ? seconds : duration - seconds);
      }, 1000);
    } else if (!active) {
      const { current: intervalIndex } = intervalRef;
      clearInterval(intervalIndex as number);
      intervalRef.current = null;
    }
  }, [active, elapsed, duration, onCount]);

  useEffect(() => {
    // if current seconds are zero coming from props,
    // that's mean played a new mix. Otherwise the mix is considered paused.
    secRef.current = current < 1 ? 0 : current;
  }, [current, duration]);

  // will unmount
  useEffect(() => {
    return () => {
      const { current: intervalIndex } = intervalRef;
      clearInterval(intervalIndex as number);
      intervalRef.current = null;
    };
  }, []);

  return visible ? (
    <span>
      {secToFormat(elapsed ? secRef.current : duration - secRef.current, timeFormat, {
        setters,
      })}
    </span>
  ) : null;
};

export default TimelineCounter;
