import React, { createContext, useState } from 'react';

type Modals = 'profile' | 'test';

type ModalActions = {
  open: CallableFunction;
  close: CallableFunction;
};

export type ModalContextProps = {
  get: (name: Modals) => ModalActions;
};

export const ModalContext = createContext<ModalContextProps>({} as any);

const ModalProvider = ({ children }: any) => {
  const [visibility, setVisibility] = useState<boolean>(false);
  const [modalName, setModalName] = useState<string>();
  const [modalParams, setModalParams] = useState();
  let Modal;

  const get = (name: Modals): ModalActions => {
    return {
      open: (params: any) => {
        setModalName(name);
        setVisibility(true);
        setModalParams(params);
      },
      close: () => setVisibility(false),
    };
  };

  Modal = modalName && require(`src/components/modals/${modalName}`).default;

  return (
    <ModalContext.Provider value={{ get }}>
      {!!Modal && visibility && (
        <Modal
          {...modalParams}
          visibility={visibility}
          close={() => setVisibility(false)}
        />
      )}
      {children}
    </ModalContext.Provider>
  );
};

export default ModalProvider;
