import React, { useState, useRef, useEffect } from 'react';
import { Icon } from 'semantic-ui-react';
import Slidify from '@atayahmet/react-slidify';
import { IEventPoint } from '@atayahmet/react-slidify/lib/utils/interfaces';
import usePlayer from 'src/hooks/usePlayer';
import { calcUnit } from 'src/utils/functors';

const Volume = () => {
  const { service } = usePlayer();
  const [isVisible, setVisibility] = useState<boolean>(false);
  const containerRef = useRef<HTMLDivElement>(null);
  const [volume, setVolume] = useState(0);
  const [height] = useState<number>(108);
  const onSlide = ({ percent: { y } }: IEventPoint, index: number) => {
    service.audio.volume = 1 - y / 100;
  };

  useEffect(() => {
    setVolume(
      calcUnit({
        left: service.audio.volume * 100,
        right: height,
      }).toNumber()
    );
  }, [isVisible, height, service.audio.volume]);

  return (
    <>
      <Icon
        link
        size="big"
        name="volume up"
        onClick={() => {
          setVisibility(!isVisible);
        }}
      />
      <div className="volume-container">
        {isVisible && (
          <div className="volume-slider">
            <div className="volume-bg-layer" ref={containerRef}>
              <Slidify
                width="12px"
                height="100px"
                points={[
                  {
                    x: 0,
                    y: 108 - volume,
                    width: 12,
                    height: 12,
                    style: {
                      marginLeft: '1px',
                      borderRadius: '7px',
                      backgroundColor: 'var(--high-text-color)',
                    },
                  },
                ]}
                axis="y"
                movable
                onSlide={onSlide}
                onStop={onSlide}
              />
            </div>
          </div>
        )}
      </div>
    </>
  );
};

export default Volume;
