import React from 'react';
import { Grid } from 'semantic-ui-react';
import Image from 'src/theme/image';
import usePlayer from 'src/hooks/usePlayer';

const Info = () => {
  const { currentMix: mix } = usePlayer();
  return (
    <Grid className="player-info reset">
      <Grid.Row columns="2" verticalAlign="middle">
        <Grid.Column width="5" className="info-image">
          <Image
            alt={`${mix?.series.name} ${mix?.count}`}
            centered
            src={mix?.image}
            width="50"
          />
        </Grid.Column>
        <Grid.Column width="10" className="info-text">
          <div className="desc word-limit bold" title={'Trance Symphony 044'}>
            {mix?.series.name} {mix?.count}
          </div>
          <div className="presenter word-limit">by {mix?.artist.name}</div>
          {mix?.description && (
            <div title={mix?.description} className="word-limit">
              {mix?.description}
            </div>
          )}
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
};

export default Info;
