import React, { useState } from 'react';
import { useTransition, animated, UseTransitionProps } from 'react-spring';
import { Card, Icon } from 'semantic-ui-react';
import { useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { ToastState } from 'src/@slices/toast';
import { remove } from 'src/@slices/toast';
import { useDispatch } from 'react-redux';
import { SemanticICONS } from 'semantic-ui-react/dist/commonjs/generic';

const iconMap = {
  success: 'check',
  warning: 'warning circle',
  error: 'warning circle',
  info: 'info',
} as Record<string, string>;

const config = { tension: 262, friction: 10, precision: 0.5, mass: 1 };

const Toast = () => {
  const [refMap] = useState(() => new WeakMap<any, HTMLDivElement>());
  const dispatch = useDispatch();
  const { messages } = useSelector<RootState, ToastState>((state) => state.toast);
  const transtions = useTransition(messages, (item: any) => item.key, {
    from: { opacity: 0, right: -100, life: '0%' },
    enter: (item: any) => async (next: any) => {
      const { offsetHeight: height } = refMap.get(item) || {};
      await next({ opacity: 1, right: 20, height });
    },
    leave: () => async (next: any) => {
      await next({ life: '100%' });
      await next({ opacity: 0, right: -150 });
    },
    onRest: (item: any, state: string, { opacity }: any) => {
      if (opacity === 1) dispatch(remove(item.key));
    },
    config: (item: any, state: string) => {
      return state === 'leave' ? [{ duration: 4000, ...config }, config] : config;
    },
  } as UseTransitionProps<any, any>);
  return (
    <div className="toast-container" style={{ top: window.pageYOffset }}>
      {transtions.map(({ item = {}, props: { life, ...styles }, key }: any, index) => (
        <animated.div
          ref={(ref) => ref && refMap.set(item, ref)}
          className="toast"
          key={index}
          style={{
            zIndex: 1,
            position: 'relative',
            margin: 20,
            ...styles,
          }}
        >
          <Card className={item.type && item.type}>
            {item.title && <Card.Content header={item.title} />}
            <Card.Content>
              {!!iconMap[item.type] && (
                <Icon name={iconMap[item.type] as SemanticICONS} size="large" />
              )}
              {item.content}
            </Card.Content>
          </Card>
        </animated.div>
      ))}
    </div>
  );
};

export default Toast;
