import React from 'react';
import { Container, Responsive, Grid } from 'semantic-ui-react';
import Mobile from './Mobile';
import Widescreen from './Widescreen';

export type Props = {
  title?: string;
  image?: string;
};

const PageHeader = ({ title, image }: Props) => {
  return (
    <Container className="pageHeader reset" fluid>
      <Responsive as={Grid} className="reset series-mobile" maxWidth="780">
        <Mobile {...{ title, image }} />
      </Responsive>
      <Responsive as={Grid} className="reset series-wide" minWidth="780">
        <Widescreen {...{ title, image }} />
      </Responsive>
    </Container>
  );
};

export default PageHeader;
