import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AppDispatch } from 'src/@store';
import Firebase from 'src/services/firebase';

export type ChangePasswordState = {
  pending: boolean;
  success?: boolean;
  error?: string | null;
  warning?: string | null;
};

const initialState = {
  pending: false,
} as ChangePasswordState;

const profileSlice = createSlice({
  name: 'changePassword',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
      state.success = false;
      state.error = null;
    },
    fulfilled(state) {
      state.pending = false;
      state.success = true;
    },
    rejected(state, action: PayloadAction<string>) {
      state.pending = false;
      state.error = action.payload;
    },
    reset() {
      return initialState;
    },
  },
});

export const changePassword = (newPassword: string) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const user = Firebase.auth().currentUser;
    await user?.updatePassword(newPassword);
    await user?.reload();
    dispatch(fulfilled());
  } catch ({ code, message }) {
    dispatch(rejected(message));
  }
};

const { actions, reducer } = profileSlice;

export const { pending, fulfilled, rejected, reset } = actions;
export default reducer;
