import React, { useState, useEffect } from 'react';
import { Container, Label } from 'semantic-ui-react';
import PageGuard from 'src/components/pageGuard';
import PageHeader from 'src/components/@partials/pageHeader';
import Gutter from 'src/theme/gutter';
import Playlist from 'src/components/playlist';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'src/@store';
import { KeyValueObject } from 'src/utils/types';
import $APP from 'src/configs/app';
import { useParams } from 'react-router-dom';
import { Mix } from 'src/models/mix';
import { fetchMixes } from 'src/@creators/mixes';
import {
  pending,
  fulfilled,
  fulfilledAppend,
  rejected,
} from 'src/@slices/artist/artistDetail';
import { dock } from 'src/utils/functors';
import { mapGenres, addSeries, addArtist } from 'src/utils/relations';
import { Genre } from 'src/models/genre';
import { Artist } from 'src/models/artist';

const { PAGINATION } = $APP;

const query = {
  orderBy: ['createdAt', 'desc'],
  limit: PAGINATION.limit,
} as KeyValueObject;

const base = (id: string = ''): any[] => [
  ['status', '==', 1],
  ['artistId', '==', id],
];

const ArtistDetailPage = () => {
  const { slug: uriSlug } = useParams<{slug: string}>();
  const dispatch = useDispatch();
  const [playlistMixes, setPlaylistMixes] = useState([]);
  const [filterItems, setFilterItems] = useState<Genre[]>([]);
  const [currentFilter, setFilter] = useState<string>();
  const {
    artists: { artists, loaded: artistsLoaded },
    genres: { genres, loaded: genresLoaded },
    series: { series: seriesList, loaded: seriesLoaded },
    artistDetail: { mixes, lastPartCount, loaded: mixesLoaded },
  } = useSelector<RootState, RootState>((state) => state);
  const artist = artists.find(({ slug }) => slug === uriSlug);

  const fetch = (id: string, lastItemId?: string) => {
    const filterClause = currentFilter
      ? [['genres', 'array-contains', currentFilter]]
      : ([] as any);
    dispatch(
      fetchMixes(
        {
          where: [...base(id), ...filterClause],
          ...query,
          startAt: lastItemId,
        },
        pending,
        fulfilledAppend,
        rejected
      )
    );
  };

  // first fetch
  useEffect(() => {
    if (artistsLoaded && artist) {
      const { id } = artist;
      dispatch(
        fetchMixes(
          {
            where: [...base(id)],
            ...query,
          },
          pending,
          fulfilled,
          rejected
        )
      );
    }
  }, [artistsLoaded, artist, dispatch]);

  // on filter
  useEffect(() => {
    if (artist) {
      const { id } = artist;
      const filterClause = currentFilter
        ? [['genres', 'array-contains', currentFilter]]
        : ([] as any);
      dispatch(
        fetchMixes(
          {
            where: [...base(id), ...filterClause],
            ...query,
          },
          pending,
          fulfilled,
          rejected
        )
      );
    }
  }, [currentFilter, artist, dispatch]);

  // load more
  useEffect(() => {
    if (mixesLoaded && seriesLoaded && artistsLoaded) {
      const series = dock(seriesList).map(mapGenres(genres)).get();
      const newMixes = dock(mixes)
        .add(addSeries(series))
        .map(mapGenres(genres))
        .add(addArtist([artist] as Artist[]))
        .get();
      setPlaylistMixes(newMixes);
    }
  }, [
    mixes,
    mixesLoaded,
    seriesLoaded,
    genresLoaded,
    artistsLoaded,
    seriesList,
    dispatch,
    genres,
    artist,
  ]);

  // filter labels
  useEffect(() => {
    if (artist && genresLoaded && artistsLoaded) {
      const { genres: ids = [] } = artist;
      const [{ genres: filterGenres }] = dock([{ genres: ids }])
        .map(mapGenres(genres))
        .get();

      setFilterItems(filterGenres.filter(Boolean));
    }
  }, [artistsLoaded, genresLoaded, genres, artist]);

  const { id: artistId, name, image } = artist || {};

  return (
    <PageGuard cond={artistsLoaded ? !!artist : true}>
      <Container fluid className="artistDetailPage reset">
        <PageHeader title={name} image={image} />
        {filterItems.length > 1 && (
          <Gutter top={1} right={1.5} bottom={0.5} left={1} key="filter-gutter">
            <Label.Group size="medium">
              {filterItems.map(({ id, name }) => (
                <Label
                  key={id}
                  onClick={() => {
                    setFilter(id);
                  }}
                  as="a"
                  color={id === currentFilter ? 'blue' : undefined}
                >
                  # {name}
                </Label>
              ))}
            </Label.Group>
          </Gutter>
        )}
        <Gutter
          top={2}
          right={1.5}
          bottom={2}
          left={2}
          style={{ overflowY: 'auto', height: 'calc(100vh - 230px)' }}
          key="list-gutter"
        >
          <Playlist
            items={playlistMixes}
            onLoad={
              lastPartCount === query.limit
                ? ({ id }: Mix) => artistId && fetch(artistId, id)
                : undefined
            }
          />
        </Gutter>
      </Container>
    </PageGuard>
  );
};

export default ArtistDetailPage;
