import { useEffect } from "react";
import * as indicator from 'src/@creators/screenIndicator';
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "src/@store";
import { APP_DEPENDENCIES } from "src/constants/screenIndicator";
import { fetchSeries } from "src/@creators/series";
import { fetchGenres } from "src/@creators/genres";
import { fetchArtists } from "src/@creators/artists";
import { fetchUserFavourites } from "src/@creators/favorites";

const DependencyProvider = () => {
  const dispatch = useDispatch();
  const {
    series: { loaded: seriesLoaded },
    genres: { loaded: genresLoaded },
    artists: { loaded: artistsLoaded },
    auth: { user }
  } = useSelector<RootState, RootState>(state => state);

  useEffect(() => {
    indicator.start(APP_DEPENDENCIES);
    dispatch(fetchSeries());
    dispatch(fetchGenres());
    dispatch(fetchArtists());
  }, [dispatch]);

  useEffect(() => {
    artistsLoaded && indicator.end(APP_DEPENDENCIES);
  }, [artistsLoaded, seriesLoaded, genresLoaded]);

  useEffect(() => {
    user && dispatch(fetchUserFavourites(user.uid));
  }, [user, dispatch]);

  return null;
};

export default DependencyProvider;
