import React from 'react';
import Image from 'src/theme/image';
import { isUrl } from 'src/utils/generic';
import { staticText } from 'src/utils/config';

type Props = {
  content?: any;
  width?: number;
  height?: number;
  className?: string;
};

const { NAME } = staticText('APP');

const Avatar = ({
  content,
  width = 50,
  height = width,
  className = '',
  ...props
}: Props) => {
  content = !content ? NAME : content;
  const validUrl = isUrl(content);
  return (
    <div
      className={`avatar ${className}`}
      style={{
        width: `${width}px`,
        height: `${height}px`,
        fontSize: `${width / 2.5}px`,
        borderRadius: `${width / 2}px`,
      }}
    >
      {validUrl && <Image circular size="small" src={content} {...props} />}
      {!validUrl && <Initials content={content} {...props} />}
    </div>
  );
};

const Initials = ({ content = NAME }: Props) => {
  const initials = content
    .match(/(\b[A-Za-z]{1})/g)
    ?.map((v: string) => v.trim())
    .filter(Boolean)
    .join('');

  return <div>{initials}</div>;
};
export default Avatar;
