import { createSlice } from '@reduxjs/toolkit';
import mixPaginationReducer, { MixPaginationState } from '../common/mixPagination';

export type FavouriteListState = MixPaginationState;

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  lastPartCount: 0,
  mixes: [],
} as FavouriteListState;

const favouriteListSlice = createSlice({
  name: 'favouriteList',
  initialState,
  reducers: mixPaginationReducer,
});

const { actions, reducer } = favouriteListSlice;

export const { pending, fulfilled, fulfilledAppend, rejected, reset } = actions;
export default reducer;
