import { ReactElement } from 'react';
import { useState } from 'react';

type Props = {
  children: CallableFunction;
  src: string;
  placeholder?: string | ReactElement;
};

const defaultImage =
  'https://yt3.ggpht.com/a/AGF-l78WViRTDtDkcrVVW8XlrrJ4MggAqM7UMKVkBQ=s288-mo-c-c0xffffffff-rj-k-no';

const Lazy = ({ children, src, placeholder = defaultImage }: Props) => {
  const [isLoaded, setIsLoaded] = useState(false);
  const [hasError, setHasError] = useState(false);
  const newImage = new Image();
  newImage.src = src;
  newImage.onload = () => setIsLoaded(true);
  newImage.onerror = () => setHasError(true);

  if (typeof placeholder === 'string') {
    return children(!isLoaded ? placeholder : src);
  }

  return isLoaded ? children(src) : hasError ? children(defaultImage) : placeholder;
};

export default Lazy;
