import React from 'react';
import secTo from 'sec-to-format';
import { timeFormat } from 'src/utils/generic';
import { Mix } from 'src/models/mix';

type Props = {
  mix: Mix;
};

const Duration = ({ mix }: Props) => {
  const { duration } = mix;
  const setters = {
    '%H': timeFormat,
    '%m': timeFormat,
    '%s': timeFormat,
  };
  return (
    <div className="duration-wrapper">{secTo(duration, '%H:%m:%s', { setters })}</div>
  );
};

export default Duration;
