import { createSlice } from '@reduxjs/toolkit';
import mixPaginationReducer, { MixPaginationState } from '../common/mixPagination';

export type ArtistDetailState = MixPaginationState;

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  lastPartCount: 0,
  mixes: [],
} as ArtistDetailState;

const artistDetailSlice = createSlice({
  name: 'artistDetail',
  initialState,
  reducers: mixPaginationReducer,
});

const { actions, reducer } = artistDetailSlice;

export const { pending, fulfilled, fulfilledAppend, rejected, reset } = actions;
export default reducer;
