import React from 'react';
import { Menu, Icon } from 'semantic-ui-react';
import { Series } from 'src/models/series';
import { NavLink } from 'react-router-dom';

type Props = {
  series: Series[];
};

const SeriesPartial = ({ series }: Props) => {
  return (
    <>
      <Menu.Item as="span" className="title">
        Series
      </Menu.Item>
      {series.map(({ name, icon, slug }, index) => (
        <Menu.Item as={NavLink} key={index} to={`/series/${slug}`}>
          <Icon name={icon} size="tiny" />
          {name}
        </Menu.Item>
      ))}
    </>
  );
};

export default SeriesPartial;
