import React, { useState } from 'react';
import { Card, Header } from 'semantic-ui-react';
import Lazy from 'src/theme/lazy';
import Loader from '../loader';
import bars from 'src/assets/images/bars.svg';
import { useHistory } from 'react-router-dom';

type Item = {
  title: string;
  subTitle?: string;
  image: string;
  link: string;
};

type Props = {
  title?: string;
  row: (item: any) => Item;
  items: any[];
};

let leftPos = 0;
const Showcase = ({ title, items, row }: Props) => {
  const history = useHistory();
  const [isActive, setIsActive] = useState(false);
  const [startPoint, setStartPoint] = useState(0);

  const onMouseDown = (e: any) => {
    setIsActive(true);
    setStartPoint(e.clientX - leftPos);
  };
  const onMouseUp = (e: any) => {
    setIsActive(false);
    setStartPoint(0);
  };
  const onMove = (e: any) => {
    e.preventDefault();
    if (!isActive || (e.movementX > -1 && leftPos >= 0)) {
      return;
    }
    const clients = e.touches ? e.touches[0] : e;
    const distance = -1 * (startPoint - clients.clientX);
    const el = document.querySelector('.showcase .ui.cards');
    el?.scroll(Math.abs(leftPos), 0);
    const totalDistance = leftPos + distance;
    leftPos = totalDistance > 0 ? 0 : distance;
  };

  const renderCards = (cards: any[]) => {
    return (
      <>
        {cards.map((item, i) => {
          const { title, subTitle, image, link } = row(item);
          return (
            <Card key={i} onClick={() => history.push(link)} link>
              <Lazy
                src={image}
                placeholder={
                  <Card.Content>
                    <Loader size="mini" src={bars} dimmer={false} active />
                  </Card.Content>
                }
              >
                {(currentSrc: string) => (
                  <Card.Content style={{ backgroundImage: `url(${currentSrc})` }} />
                )}
              </Lazy>
              <Card.Content extra>
                <Card.Header>{title}</Card.Header>
                <Card.Meta>{subTitle}</Card.Meta>
              </Card.Content>
            </Card>
          );
        })}
      </>
    );
  };

  return (
    <div className="showcase">
      {title && <Header as="h1">{title}</Header>}
      <Card.Group
        onMouseDownCapture={onMouseDown}
        onMouseUpCapture={onMouseUp}
        onMouseMove={onMove}
        onMouseLeave={() => setIsActive(false)}
      >
        {renderCards(items)}
      </Card.Group>
    </div>
  );
};

export default Showcase;
