import React, { useState, useEffect, useContext } from 'react';
import { Modal, Button, Form, Message } from 'semantic-ui-react';
import { login as authLogin } from 'src/@slices/auth/login';
import { staticText } from 'src/utils/config';
import { ReactInputEvent } from 'src/utils/types';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { AuthContext, AuthContextProps } from 'src/providers/AuthProvider';

type FormProps = {
  email: string;
  password: string;
  [key: string]: string;
};

type Props = {
  open: boolean;
  close: CallableFunction;
};

const { LOGIN_MODAL } = staticText('AUTH');

const LoginModal = ({ open = false, close = () => null }: Props) => {
  const dispatch = useDispatch();
  const { openRegisterModal } = useContext(AuthContext) as AuthContextProps;
  const { user } = useSelector((state: RootState) => state.auth);
  const { success, error: loginError, pending } = useSelector(
    (state: RootState) => state.login || {}
  );

  const [form, setForm] = useState({ email: '', password: '' } as FormProps);
  const [error, setError] = useState('');

  const onChange = ({ target }: ReactInputEvent) => {
    const { name, value } = target;
    setForm({ ...form, [name]: value });
  };

  const onClose = () => {
    close();
    setError('');
    setForm({ email: '', password: '' });
  };

  const login = () => {
    const isValid = Object.keys(form).every((key) => !!form[key]);
    setError(!isValid ? LOGIN_MODAL.FORM_EMPTY_ERROR : '');
    isValid && dispatch(authLogin(form));
  };

  useEffect(() => {
    user && close();
  }, [user, close]);

  useEffect(() => {
    loginError && setError(loginError);
  }, [pending, success, loginError]);

  const { email, password } = form;

  return (
    <Modal
      className="login-modal"
      size="mini"
      open={open}
      dimmer="blurring"
      onClose={onClose}
      closeOnEscape
      closeOnDimmerClick
      closeIcon
    >
      <Modal.Header as="h1">{LOGIN_MODAL.HEADER}!</Modal.Header>
      <Modal.Content>
        {error && <Message color="brown">{error}</Message>}
        <Form autoComplete="off">
          <Form.Input
            autoComplete="off"
            name="email"
            size="large"
            fluid
            label="Email"
            value={email}
            onChange={onChange}
            placeholder="Email"
            className={`auth-input
              ${!email ? 'empty' : ''}
              ${error && !email ? 'auth-input-error' : ''}`}
          />
          <Form.Input
            autoComplete="off"
            name="password"
            type="password"
            size="large"
            fluid
            label="Password"
            value={password}
            onChange={onChange}
            placeholder="Password"
            className={`auth-input
              ${!password ? 'empty' : ''}
              ${error && !password ? 'auth-input-error' : ''}
            `}
          />
          <Form.Checkbox label={LOGIN_MODAL.REMEMBER_TEXT} />
        </Form>
      </Modal.Content>
      <Modal.Actions>
        <Button
          positive
          size="big"
          onClick={login}
          disabled={pending}
          content={LOGIN_MODAL.BUTTON_TITLE}
        />
        <div className="login-actions-item">
          {LOGIN_MODAL.REGISTER_QUESTION_TEXT}?{' '}
          <a
            onClick={() => {
              onClose();
              openRegisterModal();
            }}
            href="#/"
          >
            {LOGIN_MODAL.REGISTER_ACTION_TEXT}
          </a>
        </div>
      </Modal.Actions>
    </Modal>
  );
};

export default LoginModal;
