import React, { useContext } from 'react';
import { Grid, Icon } from 'semantic-ui-react';
import { SidebarContext } from '../../sidebar';
import logo from './../../../assets/images/logo.svg';
import { useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import UserMenu from './UserMenu';
import { AuthContextProps, AuthContext } from 'src/providers/AuthProvider';

const Mobile = () => {
  const { toggle, visibility } = useContext(SidebarContext) as any;
  const { openLoginModal } = useContext(AuthContext) as AuthContextProps;
  const { user } = useSelector((state: RootState) => state.auth);

  return (
    <div className="mobile-navigation">
      <Grid verticalAlign="middle">
        <Grid.Row columns={3}>
          <Grid.Column width={6} verticalAlign="middle">
            <Icon
              name={!visibility ? 'sidebar' : 'times'}
              size="large"
              onClick={toggle}
            />
          </Grid.Column>
          <Grid.Column width={4} textAlign="center" verticalAlign="middle">
            <img src={logo} width="120" alt="Mix Zone" />
          </Grid.Column>
          <Grid.Column width={6} verticalAlign="middle" textAlign="right">
            <Grid>
              <Grid.Row className="mobile-right-wrapper">
                <Grid.Column
                  className="mobile-action-icons"
                  width={user ? 6 : 16}
                  verticalAlign="middle"
                  textAlign="right"
                >
                  <Icon name="search" />
                  {!user && <Icon name="user" onClick={() => openLoginModal()} />}
                </Grid.Column>
                {user && (
                  <Grid.Column
                    className="mobile-user-menu-wraper"
                    width={10}
                    verticalAlign="middle"
                  >
                    {user && <UserMenu user={user} />}
                  </Grid.Column>
                )}
              </Grid.Row>
            </Grid>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </div>
  );
};

export default Mobile;
