import React from 'react';
import { Dimmer, Image, SemanticSIZES, ImageProps } from 'semantic-ui-react';
import bars from 'src/assets/images/bars.svg';


type LoaderTypes = 'logo';

type Props = ImageProps & {
  type?: LoaderTypes;
  active?: boolean;
  dimmer?: boolean;
  src?: string;
  size?: SemanticSIZES;
};

const Loader = ({ dimmer = true, active = false, src = bars, size, ...props }: Props) => {
  return dimmer ? (
    <Dimmer active={active}>
      <Image {...props} {...{ size, src }} />
    </Dimmer>
  ) : (
    <Image {...props} {...{ size, src }} src={src} />
  );
};

export default Loader;
