import React from 'react';

const Description = ({ mix }: any) => {
  const { description, count, series: { name = null } = {} } = mix;
  return (
    <div className="description-wrapper">
      <div className="pl-desc-series">
        {name} #{count}
      </div>
      {description && (
        <div className="pl-desc word-limit" title={description}>
          {description}
        </div>
      )}
    </div>
  );
};

export default Description;
