import React, { useRef, useEffect, useState } from 'react';
import get from '@util-funcs/object-get';
import { Container, Grid, Responsive, Button, Segment } from 'semantic-ui-react';
import breakpoints from './breakpoints';
import partials from './partials';
import { KeyValueObject } from 'src/utils/types';

type Props = {
  items?: KeyValueObject[];
  onLoad?: CallableFunction;
};

const Playlist = ({ items = [], onLoad }: Props) => {
  const [isLoading, setLoading] = useState(false);
  const [containerWidth, setContainerWidth] = useState(0);
  const containerRef = useRef<HTMLDivElement>(null);
  const { columns = [] } = breakpoints.find(({ check }) => check(containerWidth)) || {};

  const onUpdate = (e?: any, data?: any) => {
    const el = containerRef.current;
    el && setContainerWidth(el.clientWidth);
  };

  useEffect(onUpdate, []);

  useEffect(() => {
    setLoading(false);
  }, [items]);

  const isButtonVisible = onLoad && items.length > 0;

  return (
    <Container fluid className="playlist">
      <div ref={containerRef}>
        <Responsive fireOnMount as={Grid} onUpdate={onUpdate}>
          {items.map((mix, i) => (
            <Grid.Row key={i} className="playlist-item">
              {columns.map(({ width, name, className = '' }, i) => {
                const Comp = get(name, partials);
                return (
                  <Grid.Column
                    key={i}
                    verticalAlign="middle"
                    width={width as any}
                    className={`${className} ${name}`}
                  >
                    {Comp && <Comp mix={mix} />}
                  </Grid.Column>
                );
              })}
            </Grid.Row>
          ))}
        </Responsive>

        <Segment style={{ opacity: !isButtonVisible ? '0' : '1' }}>
          <Button
            size="big"
            inverted
            color="red"
            fluid
            loading={isLoading}
            disabled={isLoading || !isButtonVisible}
            onClick={() => {
              setLoading(true);
              onLoad && onLoad(items[items.length - 1]);
            }}
          >
            Load More
          </Button>
        </Segment>
      </div>
    </Container>
  );
};

export default Playlist;
