import React, { useEffect } from 'react';
import { Container, Header, Tab } from 'semantic-ui-react';
import Gutter from 'src/theme/gutter';
import Profile from './tabs/Profile';
import ChangeEmail from './tabs/ChangeEmail';
import ChangePassword from './tabs/ChangePassword';
import Avatar from 'src/components/avatar';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'src/@store';
import { fetchProfile, ProfileState, reset as resetProfile } from 'src/@slices/account/profile';
import { reset as resetChangeEmail } from 'src/@slices/account/changeEmail';
import { reset as resetChangePassword } from 'src/@slices/account/changePassword';

const Account = () => {
  const dispatch = useDispatch();
  const { profile: user } = useSelector<RootState, ProfileState>(
    ({ profile }) => profile
  );

  useEffect(() => {
    dispatch(fetchProfile());
    return () => {
      dispatch(resetProfile());
      dispatch(resetChangeEmail());
      dispatch(resetChangePassword());
    };
  }, [dispatch]);

  const panes = [
    {
      menuItem: 'Profile',
      pane: (
        <Tab.Pane key="profile" attached={false}>
          {user && <Profile profile={user} />}
        </Tab.Pane>
      ),
    },
    {
      menuItem: 'Change Email',
      pane: (
        <Tab.Pane key="changeEmail" attached={false}>
          <ChangeEmail email={user?.email} />
        </Tab.Pane>
      ),
    },
    {
      menuItem: 'Change Password',
      pane: (
        <Tab.Pane key="changePassword" attached={false}>
          <ChangePassword />
        </Tab.Pane>
      ),
    },
  ];

  const { displayName, photoUrl } = user || {};

  return (
    <Container fluid className="account">
      <Container fluid textAlign="center" className="cover-area">
        <Header as="h1">My Account</Header>
        <Avatar width={100} className="margin-auto" content={photoUrl || displayName} />
      </Container>
      {user && (
        <Gutter>
          <Tab
            renderActiveOnly={false}
            className="tab-container"
            menu={{ secondary: true, pointing: true }}
            panes={panes}
          />
        </Gutter>
      )}
    </Container>
  );
};

export default Account;
