import React from 'react';
import { Grid, Icon } from 'semantic-ui-react';
import Volume from './partials/volume';
import usePlayerHistory from 'src/hooks/usePlayerHistory';
import usePlayer from 'src/hooks/usePlayer';

type Props = {
  visibilities: Record<string, boolean>;
  extra: Record<string, boolean>;
};

const Controls = ({ visibilities = {}, extra = {} }: Props) => {
  const { isPlaying, isLoading, play, back, forward, currentMix } = usePlayer();
  const {
    canGoBack,
    canGoForward,
    equalToActiveHistory,
    getSeconds,
  } = usePlayerHistory();
  const { volume = true } = visibilities;
  const { iconSize = 'big' } = extra;
  let sub = 0;
  !volume && sub++;
  return (
    <Grid className="player-controls">
      <Grid.Row columns={(4 - sub) as any}>
        <Grid.Column className="reset-column">
          <Icon
            link
            size={iconSize as any}
            name="step backward"
            disabled={isLoading || !canGoBack}
            onClick={back}
          />
        </Grid.Column>
        <Grid.Column className="reset-column">
          <Icon
            link
            size={iconSize as any}
            name={isPlaying ? 'pause' : isLoading ? 'sync alternate' : 'play'}
            loading={isLoading}
            disabled={isLoading}
            onClick={() => {
              play(currentMix, equalToActiveHistory(currentMix?.id) ? getSeconds() : 0);
            }}
          />
        </Grid.Column>
        <Grid.Column className="reset-column">
          <Icon
            link
            size={iconSize as any}
            name="step forward"
            disabled={isLoading || !canGoForward}
            onClick={forward}
          />
        </Grid.Column>
        {volume && (
          <Grid.Column className="reset-column" textAlign="center">
            <Volume />
          </Grid.Column>
        )}
      </Grid.Row>
    </Grid>
  );
};

export default Controls;
