import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Artist } from 'src/models/artist';

export type ArtistsState = {
  pending: boolean;
  loaded: boolean;
  error: string | null;
  artists: Artist[];
};

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  artists: [],
} as ArtistsState;

const artistSlice = createSlice({
  name: 'artists',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled(state, action: PayloadAction<Artist[]>) {
      state.artists = action.payload;
      state.pending = false;
      state.loaded = true;
    },
    rejected(state, action: PayloadAction<any>) {
      state.error = action.payload;
      state.pending = false;
    },
  },
});

const { actions, reducer } = artistSlice;

export const { pending, fulfilled, rejected } = actions;
export default reducer;
