import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Mix } from 'src/models/mix';

export type MainPlayerState = {
  history: Mix[];
  currentMix: Mix | null;
  isSilent: boolean;
  isPlaying: boolean;
  isPaused: boolean;
  isVisible: boolean;
  isLoading: boolean;
  isLoaded: boolean;
  hasError: boolean;
};

const initialState = {
  history: [],
  currentMix: null,
  isSilent: false,
  isPlaying: false,
  isPaused: false,
  isVisible: true,
  isLoading: false,
  isLoaded: false,
  hasError: false,
} as MainPlayerState;

const mainPlayerSlice = createSlice({
  name: 'mainPlayer',
  initialState,
  reducers: {
    loadSilent(state, action: PayloadAction<Mix>) {
      state.currentMix = action.payload;
      state.isLoaded = false;
      state.hasError = false;
      state.isSilent = true;
    },
    loadStart(state, action: PayloadAction<Mix>) {
      state.currentMix = action.payload;
      state.isLoading = true;
      state.isLoaded = false;
      state.isPlaying = false;
      state.hasError = false;
      state.isSilent = false;
    },
    loadError(state) {
      state.isPlaying = false;
      state.isLoading = false;
      state.hasError = true;
    },
    dataLoaded(state) {
      state.isLoading = false;
      state.isLoaded = true;
      state.hasError = false;
    },
    play(state) {
      state.isPlaying = false;
      state.isLoading = false;
      state.isSilent = false;
    },
    playing(state) {
      state.isPlaying = true;
      state.isLoading = false;
      state.isLoaded = true;
      state.isPaused = false;
      state.isSilent = false;
    },
    waiting(state) {
      state.isLoading = true;
      state.isLoaded = false;
      state.isPlaying = false;
    },
    paused(state) {
      state.isPlaying = false;
      state.isPaused = true;
    },
    visibility(state, action: PayloadAction<boolean>) {
      state.isVisible = action.payload;
    },
  },
});

const { actions, reducer } = mainPlayerSlice;

export const {
  paused,
  playing,
  loadStart,
  loadSilent,
  loadError,
  dataLoaded,
  play,
  waiting,
} = actions;
export default reducer;
