import React, { createContext, useState } from 'react';
import LoginModal from 'src/components/modals/login';
import RegisterModal from 'src/components/modals/register';
import { useSelector } from 'react-redux';
import { Redirect, Route, RouteProps } from 'react-router-dom';
import { RootState } from 'src/@store';

type Props = {
  children: React.ReactChild;
};

export type AuthContextProps = {
  closeLoginModal: CallableFunction;
  openLoginModal: CallableFunction;
  openRegisterModal: CallableFunction;
  closeRegisterModal: CallableFunction;
  PrivateRoute: React.ComponentType<RouteProps>;
  // login: (email: string, password: string) => void;
};

export const AuthContext = createContext({});

AuthContext.displayName = 'Authentication Context';

const PrivateRoute = ({ children, ...props }: Props) => {
  const user = useSelector<RootState>(({ auth }) => auth.user);
  return (
    <Route
      {...props}
      render={() => {
        return user ? children : <Redirect to="/" />;
      }}
    />
  );
};

const AuthProvider = ({ children }: Props) => {
  const [loginModalVisibility, setLoginModalVisibility] = useState(false);
  const [registerModalVisibility, setRegisterModalVisibility] = useState(false);
  const openLoginModal = () => setLoginModalVisibility(true);
  const closeLoginModal = () => setLoginModalVisibility(false);

  const openRegisterModal = () => setRegisterModalVisibility(true);
  const closeRegisterModal = () => setRegisterModalVisibility(false);

  return (
    <AuthContext.Provider
      value={
        {
          PrivateRoute,
          openLoginModal,
          closeLoginModal,
          openRegisterModal,
          closeRegisterModal,
        } as AuthContextProps
      }
    >
      <>
        <LoginModal close={closeLoginModal} open={loginModalVisibility} />
        <RegisterModal close={closeRegisterModal} open={registerModalVisibility} />
        {children}
      </>
    </AuthContext.Provider>
  );
};

export default AuthProvider;
