import dayjs from 'dayjs';
import { KeyValueObject, DateFormat } from 'src/utils/types';
import Model from './model';
import { Series } from './series';
import { Genre } from './genre';
import { Artist } from './artist';

export type Mix = {
  id?: string;
  artistId: string;
  seriesId: string;
  sourceId: string;
  sourceType: number;
  sourceUrl: string;
  trackListId: string;
  count: number;
  createdAt: DateFormat;
  image: string;
  duration: number;
  description: string;
  artist: Artist;
  series: Series;
  genres: Genre[];
};

export default class MixModel extends Model implements Mix {
  public id: string;
  public artistId: string;
  public seriesId: string;
  public sourceId: string;
  public sourceType: number;
  public sourceUrl: string;
  public trackListId: string;
  public count: number;
  public createdAt: { seconds: number; nanoseconds: number };
  public image: string;
  public duration: number;
  public description: string;
  public series: Series;
  public artist: Artist;
  public genres: Genre[];

  constructor({
    id,
    artistId,
    seriesId,
    sourceId,
    sourceType,
    sourceUrl,
    trackListId,
    count,
    createdAt,
    image,
    duration,
    description,
    series,
    artist,
    genres,
  }: KeyValueObject) {
    super();
    this.id = id;
    this.artistId = artistId;
    this.seriesId = seriesId;
    this.sourceId = sourceId;
    this.sourceType = sourceType;
    this.sourceUrl = sourceUrl;
    this.trackListId = trackListId;
    this.count = count;
    this.createdAt = this.formatDate(createdAt);
    this.image = image;
    this.duration = duration;
    this.description = description || '';
    this.series = series;
    this.artist = artist;
    this.genres = genres;
  }

  private formatDate(date: DateFormat): DateFormat {
    return {
      ...date,
      label: dayjs.unix(date.seconds).format('D.MM.YYYY'),
    };
  }
}
