import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ToastMessage } from 'src/utils/types';

export type ToastState = {
  messages: Array<ToastMessage>;
};

const initialState = {
  messages: [],
} as ToastState;

const toastSlice = createSlice({
  name: 'toast',
  initialState,
  reducers: {
    publish(state, action: PayloadAction<ToastMessage>) {
      const { length } = state.messages;
      const { key = length } = action.payload;
      state.messages.push({ ...action.payload, key });
    },
    remove(state, action: PayloadAction<any>) {
      const { messages } = state;
      const { payload: toastKey } = action;
      state.messages = messages.filter(({ key }) => key !== toastKey);
    },
    reset() {
      return initialState;
    },
  },
});

const { actions, reducer } = toastSlice;

export const { publish, remove, reset } = actions;
export default reducer;
