import React from 'react';
import Showcase from 'src/components/showcase';
import { useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { ArtistsState } from 'src/@slices/artist/artists';
import { Artist } from 'src/models/artist';

const ArtistShowcase = () => {
  const { artists, loaded } = useSelector<RootState, ArtistsState>(
    (state) => state.artists
  );

  return (
    <Showcase
      items={artists}
      title={loaded ? 'Explore DJs' : ''}
      row={(artist: Artist) => ({
        link: `/artists/${artist.slug}`,
        title: artist.name,
        image: artist.image,
        subTitle: artist.lastMixTitle,
      })}
    />
  );
};

export default ArtistShowcase;
