import { Mix } from 'src/models/mix';
import { useSelector, useDispatch } from 'react-redux';
import {
  PlayerHistoryState,
  push,
  forward,
  back,
} from 'src/@slices/player/playerHistory';
import { RootState } from 'src/@store';

const usePlayerHistory = () => {
  const dispatch = useDispatch();
  const { history, pointer } = useSelector<RootState, PlayerHistoryState>(
    (state) => state.playerHistory
  );

  const saveSeconds = (seconds: number) => {
    if (Math.floor(seconds % 5) === 0) {
      sessionStorage.setItem('seconds', String(seconds));
    }
  };

  const getSeconds = () => parseInt(sessionStorage.getItem('seconds') || '0');

  const equalToActiveHistory = (id?: string) => {
    const mix = history[(pointer ?? 0)];
    if (!mix) return false;
    return mix.id === id;
  };

  return {
    saveSeconds,
    getSeconds,
    push: (mix: Mix) => dispatch(push(mix)),
    forward: () => dispatch(forward()),
    back: () => dispatch(back()),
    canGoBack: history[(pointer ?? 0) - 1],
    canGoForward: history[(pointer ?? 0) + 1],
    length: history.length,
    history,
    pointer,
    equalToActiveHistory,
  };
};

export default usePlayerHistory;
