import { PayloadAction } from "@reduxjs/toolkit";
import { Mix } from "src/models/mix";

export type MixPaginationState = {
  pending: boolean;
  loaded: boolean;
  error: string | null;
  lastPartCount: number;
  mixes: Array<Mix>;
};

const mixPaginationReducer = {
  pending(state: MixPaginationState) {
    state.pending = true;
  },
  fulfilled(state: MixPaginationState, action: PayloadAction<Mix[]>) {
    state.mixes = action.payload;
    state.lastPartCount = action.payload.length;
    state.pending = false;
    state.loaded = true;
  },
  fulfilledAppend(state: MixPaginationState, action: PayloadAction<Mix[]>) {
    state.mixes = [...state.mixes, ...action.payload];
    state.lastPartCount = action.payload.length;
    state.pending = false;
    state.loaded = true;
  },
  rejected(state: MixPaginationState, action: PayloadAction<any>) {
    state.error = action.payload;
    state.pending = false;
  },
  reset(state: MixPaginationState) {
    state.mixes = [];
  }
};

export default mixPaginationReducer;
