import Firebase from 'src/services/firebase';
import { AppDispatch } from 'src/@store';
import { pending, rejected, fulfilled } from '../@slices/artist/artists';
import { toData } from 'src/utils/firebase';
import { Artist } from 'src/models/artist';

export const fetchArtists = () => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const artists = Firebase.db.collection('artists');
    const { docs = [] } = await artists.orderBy('lastMixCreatedAt', 'desc').get();
    const data = toData<Artist>(docs);
    dispatch(fulfilled(data));
  } catch ({ message }) {
    dispatch(rejected(message));
  }
};
