export const Try = (
  f: CallableFunction,
  right: CallableFunction,
  left: CallableFunction
) => {
  try {
    var result = f();
    return right(result);
  } catch (e) {
    return left(e);
  }
};

export const isUrl = (val: string = '') => val.includes('http');

export const shuffle = (data: any[] = []) => data.sort(() => Math.random() - .5);

export const secToTime = (seconds: number) => {
  const hours = Math.floor(seconds / 3600).toString().padStart(2, '0').slice(-2);
  const minutes = Math.floor(seconds / 60 % 60).toString().padStart(2, '0').slice(-2);
  const secs = Math.floor(seconds % 60).toString().padStart(2, '0').slice(-2);
  return `${hours}:${minutes}:${secs}`;
};

export const timeFormat = (val: number) => val < 10 ? `0${val}` : val;

export const isNull = (val: any): boolean => val === null;
