import React, { useState, useEffect, useContext } from 'react';
import { Modal, Button, Form, Message } from 'semantic-ui-react';
import { register as authRegister } from 'src/@slices/auth/register';
import { staticText } from 'src/utils/config';
import { ReactInputEvent } from 'src/utils/types';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { AuthContext, AuthContextProps } from 'src/providers/AuthProvider';

type FormProps = {
  email: string;
  password: string;
  [key: string]: string;
};

type Props = {
  open: boolean;
  close: CallableFunction;
};

const {
  REGISTER_MODAL: {
    HEADER,
    DEFAULT_SUBJECT,
    FORM_EMPTY_ERROR,
    LOGIN_QUESTION_TEXT,
    LOGIN_ACTION_TEXT,
  },
} = staticText('AUTH');

const RegisterModal = ({ open = false, close }: Props) => {
  const dispatch = useDispatch();
  const { user } = useSelector((state: RootState) => state.auth);
  const { success, error: registerError, pending } = useSelector(
    (state: RootState) => state.register
  );
  const [form, setForm] = useState({ name: '', email: '', password: '' } as FormProps);
  const [error, setError] = useState('');
  const [subject, setSubject] = useState(DEFAULT_SUBJECT);

  const onChange = ({ target }: ReactInputEvent) => {
    const { name, value } = target;
    setForm({ ...form, [name]: value });
  };

  const onClose = () => {
    close();
    setError('');
    setSubject(DEFAULT_SUBJECT);
    setForm({ name: '', email: '', password: '' });
  };

  const register = () => {
    const isValid = Object.keys(form).every((key) => !!form[key]);
    setError(!isValid ? FORM_EMPTY_ERROR : '');
    isValid && dispatch(authRegister({ ...form, sendVerifyEmail: true }));
  };

  useEffect(() => {
    user && close();
  }, [user, close]);

  useEffect(() => {
    registerError && setError(registerError);
  }, [success, pending, registerError]);

  const { name, email, password } = form;
  const { openLoginModal } = useContext(AuthContext) as AuthContextProps;

  return (
    <Modal
      className="login-modal"
      size="mini"
      open={open}
      dimmer="blurring"
      onClose={onClose}
      closeOnEscape
      closeOnDimmerClick
      closeIcon
    >
      <Modal.Header as="h1">{HEADER.replace('{0}', subject)}!</Modal.Header>
      <Modal.Content>
        {error && <Message color="brown">{error}</Message>}
        <Form autoComplete="off">
          <Form.Input
            autoComplete="off"
            name="name"
            size="large"
            fluid
            label="Name"
            value={name}
            onChange={onChange}
            onBlur={() => setSubject(name || DEFAULT_SUBJECT)}
            placeholder="Name"
            className={`auth-input
              ${!name ? 'empty' : ''}
              ${error && !name ? 'auth-input-error' : ''}`}
          />
          <Form.Input
            autoComplete="off"
            name="email"
            size="large"
            fluid
            label="Email"
            value={email}
            onChange={onChange}
            placeholder="Email"
            className={`auth-input
              ${!email ? 'empty' : ''}
              ${error && !email ? 'auth-input-error' : ''}`}
          />
          <Form.Input
            autoComplete="off"
            name="password"
            type="password"
            size="large"
            fluid
            label="Password"
            value={password}
            onChange={onChange}
            placeholder="Password"
            className={`auth-input
              ${!password ? 'empty' : ''}
              ${error && !password ? 'auth-input-error' : ''}
            `}
          />
        </Form>
      </Modal.Content>
      <Modal.Actions>
        <Button positive content="Register" size="big" onClick={register} />
        <div className="login-actions-item">
          {LOGIN_QUESTION_TEXT}?{' '}
          <a
            onClick={() => {
              onClose();
              openLoginModal();
            }}
            href="#/"
          >
            {LOGIN_ACTION_TEXT}
          </a>
        </div>
      </Modal.Actions>
    </Modal>
  );
};

export default RegisterModal;
