import React from 'react';
import Loader from '../loader';
import { useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { ScreenIndicatorState } from 'src/@slices/screenIndicator';
import logo from 'src/assets/images/logo.svg';

const ScreenIndicator = () => {
  const { isEmpty } = useSelector<RootState, ScreenIndicatorState>(
    (state) => state.screenIndicator
  );

  return !isEmpty ? <Loader src={logo} active type="logo" /> : null;
};

export default ScreenIndicator;
