import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AppDispatch } from 'src/@store';
import Firebase from 'src/services/firebase';
import { update as updateAuth } from '../auth/auth';
import { ProfileModel } from 'src/models/profile';

export type ProfileState = {
  pending: boolean;
  success?: boolean;
  error?: string | null;
  profile?: ProfileModel | null;
};

const initialState = {
  pending: false,
  profile: null,
} as ProfileState;

const profileSlice = createSlice({
  name: 'profile',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
      state.success = false;
      state.error = null;
    },
    fulfilled(state, action: PayloadAction<ProfileModel | null>) {
      state.profile = action.payload;
      state.pending = false;
      state.error = null;
    },
    success(state) {
      state.success = true;
    },
    rejected(state, action: PayloadAction<string>) {
      state.pending = false;
      state.error = action.payload;
    },
    reset() {
      return initialState;
    }
  },
});

export const fetchProfile = () => (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    dispatch(fulfilled(Firebase.auth().currentUser as any));
  } catch (e) {
    dispatch(rejected({ ...e }));
  }
};

type UpdateProps = {
  displayName: string;
  photoUrl: string;
};

export const update = (form: UpdateProps) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const { displayName, photoUrl: photoURL } = form;
    const user = Firebase.auth().currentUser;

    await user?.updateProfile({
      displayName,
      photoURL,
    });

    await user?.reload();
    const profile = Firebase.currentUser;

    dispatch(success());
    dispatch(fulfilled(profile as Record<string, any>));
    dispatch(updateAuth(profile));
  } catch ({ message}) {
    dispatch(rejected(message));
  }
};

const { actions, reducer } = profileSlice;

export const { pending, fulfilled, rejected, reset, success } = actions;
export default reducer;
