import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import Firebase, { FirebaseErrorProps } from 'src/services/firebase';
import { AppDispatch } from 'src/@store';
import { fetchUserFavourites } from 'src/@creators/favorites';

type LoginState = {
  pending: boolean;
  success?: boolean;
  error?: string;
};

const initialState = {
  pending: false,
} as LoginState;

const loginSlice = createSlice({
  name: 'login',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled(state) {
      state.pending = false;
      state.success = true;
    },
    rejected(state, action: PayloadAction<FirebaseErrorProps>) {
      const { message } = action.payload;
      state.error = message;
      state.pending = false;
      state.success = false;
    },
  },
});

export const login = (auth: any) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    await Firebase.persistentce();
    const { email: userEmail, password: userPassword } = auth;
    const { user } = await Firebase.login(userEmail, userPassword);
    dispatch(fulfilled());
    user && fetchUserFavourites(user.uid)(dispatch);
  } catch (e) {
    dispatch(rejected(e.toJSON()));
  }
};

const { actions, reducer } = loginSlice;

export const { pending, fulfilled, rejected } = actions;
export default reducer;
