import Subject from '@atayahmet/observer-js';

class PlayerService {
  private audioEl: HTMLAudioElement = new Audio();
  public onLoadedData: Subject = new Subject();
  public onLoadStart: Subject = new Subject();
  public onPlay: Subject = new Subject();
  public onPlaying: Subject = new Subject();
  public onPause: Subject = new Subject();
  public onWaiting: Subject = new Subject();
  private onTimeUpdate: Subject = new Subject();
  public onError: Subject = new Subject();
  public onAbort: Subject = new Subject();

  constructor() {
    this.createEvents();
  }

  get audio(): HTMLAudioElement {
    return this.audioEl;
  }

  public load(src: string) {
    this.audioEl.src = src;
    this.audioEl.load();
    // this.track = this.audio.addTextTrack('metadata');
    return this;
  }

  public play(): void {
    this.audioEl.play();
  }

  public pause(): void {
    this.audioEl.pause();
  }

  private createEvents() {
    this.audioEl.addEventListener('loadeddata', () => this.onLoadedData.notify());
    this.audioEl.addEventListener('loadstart', () => this.onLoadStart.notify());
    this.audioEl.addEventListener('pause', () => this.onPause.notify());
    this.audioEl.addEventListener('play', () => this.onPlay.notify());
    this.audioEl.addEventListener('playing', () => this.onPlaying.notify());
    this.audioEl.addEventListener('waiting', () => this.onWaiting.notify());
    this.audioEl.addEventListener('timeupdate', () => this.onTimeUpdate.notify());
    this.audioEl.addEventListener('error', e => this.onError.notify(e));
    this.audioEl.addEventListener('abort', e => this.onAbort.notify(e));
  }
}

export default new PlayerService();
