import React, { useState, useRef, useEffect } from 'react';
import { Container, Grid, Responsive } from 'semantic-ui-react';
import breakpoints, { defaultPoint } from './breakpoints';
import Gutter from 'src/theme/gutter';
import ExploreDJs from './exploreDjs';
import RecentlyAdded from './recentlyAdded';
import Mood from './mood';

type OnUpdateProps = {
  width?: number;
};

const HomePage = () => {
  const [innerWidth, setInnerWidth] = useState(0);
  const containerRef = useRef<HTMLDivElement>(null);
  const point = breakpoints.find(({ check }) => check(innerWidth));

  const onUpdate = (e?: any, data: OnUpdateProps = {}) => {
    const { width } = data;
    width && setInnerWidth(width);
  };

  const {
    genres: { classNames: genresContainerClassNames },
    playlist: { classNames: playlistContainerClassNames },
  } = point || defaultPoint;

  useEffect(onUpdate, []);

  return (
    <Gutter ref={containerRef}>
      <Responsive fluid className="home" fireOnMount as={Container} onUpdate={onUpdate}>
        <ExploreDJs />
        <Container fluid className="inner-wrapper">
          <Grid stackable>
            <Grid.Row columns={2}>
              <Grid.Column
                className={`pl-wrapper ${playlistContainerClassNames}`}
                mobile={8}
                tablet={8}
                computer={16}
                widescreen={8}
                largeScreen={8}
              >
                <RecentlyAdded />
              </Grid.Column>
              <Grid.Column
                mobile={8}
                tablet={8}
                computer={16}
                widescreen={8}
                largeScreen={8}
                className={genresContainerClassNames}
              >
                <Mood />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </Responsive>
    </Gutter>
  );
};

export default HomePage;
