import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AppDispatch } from 'src/@store';
import Firebase from 'src/services/firebase';
import { update as updateAuth } from '../auth/auth';
import { fulfilled as profileFulfilled } from './profile';

export type ChangeEmailState = {
  pending: boolean;
  success?: boolean;
  error?: string | null;
  warning?: string | null;
  email?: string | null;
};

const initialState = {
  pending: false,
  email: null,
} as ChangeEmailState;

const changeEmailSlice = createSlice({
  name: 'changeEmail',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
      state.success = false;
      state.error = null;
    },
    fulfilled(state, action: PayloadAction<string | null>) {
      state.email = action.payload;
      state.pending = false;
      state.success = true;
    },
    rejected(state, action: PayloadAction<string>) {
      state.pending = false;
      state.error = action.payload;
    },
    reset() {
      return initialState;
    },
  },
});

export const changeEmail = (newEmail: string) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const user = Firebase.auth().currentUser;
    await user?.updateEmail(newEmail);
    await user?.reload();
    await user?.sendEmailVerification();
    const profile = Firebase.currentUser;
    dispatch(updateAuth(profile));
    dispatch(profileFulfilled(profile as Record<string, any>));
    dispatch(fulfilled(profile.email));
  } catch ({ code, message }) {
    dispatch(rejected(message));
  }
};

const { actions, reducer } = changeEmailSlice;

export const { pending, fulfilled, rejected, reset } = actions;
export default reducer;
