import { createSlice } from '@reduxjs/toolkit';
import mixReducer, { MixState } from '../common/mix';

export type LastMixesOfArtistsState = MixState;

const initialState = {
  pending: false,
  loaded: false,
  normalized: false,
  error: null,
  mixes: [],
} as LastMixesOfArtistsState;

const lastMixesOfArtistsSlice = createSlice({
  name: 'lastMixesOfArtists',
  initialState,
  reducers: mixReducer,
});

const { actions, reducer } = lastMixesOfArtistsSlice;

export const { pending, fulfilled, normalized, rejected } = actions;
export default reducer;
