import { PayloadAction } from '@reduxjs/toolkit';
import { Mix } from 'src/models/mix';

export type MixState = {
  pending: boolean;
  loaded: boolean;
  normalized: boolean;
  error: string | null;
  mixes: Mix[];
};

const mixReducer = {
  pending(state: MixState) {
    state.pending = true;
  },
  normalized(state: MixState, action: PayloadAction<Mix[]>) {
    state.mixes = action.payload;
    state.normalized = true;
  },
  fulfilled(state: MixState, action: PayloadAction<Mix[]>) {
    state.mixes = action.payload;
    state.pending = false;
    state.loaded = true;
    state.normalized = false;
  },
  rejected(state: MixState, action: PayloadAction<any>) {
    state.error = action.payload;
    state.pending = false;
  },
};

export default mixReducer;
