import { createSlice } from '@reduxjs/toolkit';
import mixReducer, { MixState } from '../common/mix';

export type RecentlyAddedMixesState = MixState;

const initialState = {
  pending: false,
  loaded: false,
  normalized: false,
  error: null,
  mixes: [],
} as RecentlyAddedMixesState;

const recentlyAddedMixesSlice = createSlice({
  name: 'recentlyAddedMixes',
  initialState,
  reducers: mixReducer,
});

const { actions, reducer } = recentlyAddedMixesSlice;

export const { pending, fulfilled, normalized, rejected } = actions;
export default reducer;
