import React, { forwardRef } from 'react';
import { KeyValueObject } from 'src/utils/types';

type Props = {
  children: React.ReactChild;
  ref?: React.Ref<any>;
  top?: number;
  bottom?: number;
  left?: number;
  right?: number;
  style?: KeyValueObject;
};

const Gutter = forwardRef(({ children, top, bottom, left, right, style = {}, ...props }: Props, ref) => {
  return (
    <div
      ref={ref as any}
      className="gutter"
      style={{
        ...style,
        paddingRight: `${right}em`,
        paddingLeft: `${left}em`,
        paddingTop: `${top}em`,
        paddingBottom: `${bottom}em`,
      }}
      {...props}
    >
      {children}
    </div>
  );
});

Gutter.defaultProps = {
  top: 1,
  left: 1,
  right: 1,
  bottom: 1,
};

export default Gutter;
