import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export type UserFavouritesState = {
  pending: boolean;
  loaded: boolean;
  error: string | null;
  ids: string[];
};

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  ids: [],
} as UserFavouritesState;

const userFavouritesSlice = createSlice({
  name: 'userFavourites',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled(state, action: PayloadAction<string[]>) {
      state.ids = action.payload;
      state.pending = false;
      state.loaded = true;
    },
    rejected(state, action: PayloadAction<any>) {
      state.error = action.payload;
      state.pending = false;
    },
    addPending(state) {
      state.pending = true;
    },
    addFulfilled(state, action: PayloadAction<string>) {
      state.pending = false;
      const { payload: mixId } = action;
      !state.ids.includes(mixId) && state.ids.push(mixId);
    },
    addRejected(state, action: PayloadAction<any>) {
      state.pending = false;
      state.error = action.payload;
    },
    removePending(state) {
      state.pending = true;
    },
    removeFulfilled(state, action: PayloadAction<string>) {
      state.pending = false;
      const { payload: mixId } = action;
      const index = state.ids.findIndex((id) => id === mixId);
      if (index !== -1) state.ids.splice(index, 1);
    },
    removeRejected(state, action: PayloadAction<any>) {
      state.pending = false;
      state.error = action.payload;
    },
  },
});

const { actions, reducer } = userFavouritesSlice;

export const {
  pending,
  fulfilled,
  rejected,
  addFulfilled,
  addPending,
  addRejected,
  removeFulfilled,
  removePending,
  removeRejected,
} = actions;
export default reducer;
