import { createSlice } from '@reduxjs/toolkit';
import mixPaginationReducer, { MixPaginationState } from '../common/mixPagination';

export type SeriesDetailState = MixPaginationState;

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  lastPartCount: 0,
  mixes: [],
} as SeriesDetailState;

const seriesDetailSlice = createSlice({
  name: 'seriesDetail',
  initialState,
  reducers: mixPaginationReducer,
});

const { actions, reducer } = seriesDetailSlice;

export const { pending, fulfilled, fulfilledAppend, rejected, reset } = actions;
export default reducer;
