import { createSlice } from '@reduxjs/toolkit';
import mixPaginationReducer, { MixPaginationState } from '../common/mixPagination';

export type GenreDetailState = MixPaginationState;

const initialState = {
  pending: false,
  loaded: false,
  error: null,
  lastPartCount: 0,
  mixes: [],
} as GenreDetailState;

const genreDetailSlice = createSlice({
  name: 'genreDetail',
  initialState,
  reducers: mixPaginationReducer,
});

const { actions, reducer } = genreDetailSlice;

export const { pending, fulfilled, fulfilledAppend, rejected, reset } = actions;
export default reducer;
