import Firebase from 'src/services/firebase';
import { AppDispatch } from 'src/@store';
import { pending, rejected, fulfilled, featuredFulfilled } from '../@slices/genre/genres';
import { toData } from 'src/utils/firebase';
import { Genre } from 'src/models/genre';

export const fetchGenres = () => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    const genres = Firebase.db.collection('genres').where('isActive', '==', true);
    const { docs = [] } = await genres.get();
    const data = toData<Genre>(docs);
    dispatch(fulfilled(data));
    dispatch(featuredFulfilled(data));
  } catch ({ message }) {
    dispatch(rejected(message));
  }
};
