import React from 'react';
import { Dropdown, Icon } from 'semantic-ui-react';
import usePlayer from 'src/hooks/usePlayer';
import useFavorites from 'src/hooks/useFavorites';
import { staticText } from 'src/utils/config';

type Props = {
  extra: {
    actionName?: string;
  };
};

const Actions = ({ extra, ...props }: Props) => {
  const { actionName } = extra;
  const { currentMix } = usePlayer();
  const { has, toggle } = useFavorites();
  const { ACTIONS: $A } = staticText('MAIN_PLAYER');

  return (
    <div className="player-actions">
      {!actionName && (
        <Dropdown item direction="left" icon="bars">
          <Dropdown.Menu>
            <Dropdown.Item
              icon="heart"
              onClick={() => currentMix?.id && toggle(currentMix?.id)}
              text={
                has(currentMix?.id) ? $A.REMOVE_FROM_FAVOURITES : $A.ADD_TO_FAVOURITES
              }
            />
          </Dropdown.Menu>
        </Dropdown>
      )}
      {actionName === 'go-to-detail' && <Icon name="angle up" link size="large" />}
    </div>
  );
};

export default Actions;
