import React from 'react';
import { Container, Responsive, Grid } from 'semantic-ui-react';
import { KeyValueObject } from 'src/utils/types';
import breakpoints from './breakpoints';
import partials from './partials';
import get from '@util-funcs/object-get';
import MiniTimeline from './partials/miniTimeline';

class MainPlayer extends React.Component<any, KeyValueObject> {
  state: Readonly<KeyValueObject> = {
    containerWidth: 0,
  };

  private onUpdate = (e?: any, data?: any) => {
    this.setState({
      containerWidth: window.innerWidth,
    });
  };

  public render() {
    const { containerWidth } = this.state;
    const { columns = [] } = breakpoints.find(({ check }) => check(containerWidth)) || {};
    return (
      <Container fluid className="mainPlayer reset">
        <Responsive
          fireOnMount
          as={Grid}
          className="reset full-height"
          onUpdate={this.onUpdate}
        >
          {!columns.find(({ name }) => name === 'timeline') && (
            <Grid.Row className="mini-timeline">
              <Grid.Column>
                <MiniTimeline />
              </Grid.Column>
            </Grid.Row>
          )}
          <Grid.Row>
            {columns.map(
              ({ width, name, className = '', visibilities = {}, extra = {} }, i) => {
                const Comp = get(name, partials);
                return (
                  <Grid.Column
                    key={i}
                    verticalAlign="middle"
                    width={width as any}
                    className={`${className} ${name}`}
                  >
                    {Comp && <Comp {...{ visibilities, extra }} />}
                  </Grid.Column>
                );
              }
            )}
          </Grid.Row>
        </Responsive>
      </Container>
    );
  }
}

export default MainPlayer;
