import store from 'src/@store';
import { publish } from 'src/@slices/toast';

export const success = (content: string, title?: string) =>
  store.dispatch(publish({ title, type: 'success', content }));

export const error = (content: string, title?: string) =>
  store.dispatch(publish({ title, type: 'error', content }));

export const warning = (content: string, title?: string) =>
  store.dispatch(publish({ title, type: 'warning', content }));

export const notify = (content: string, title?: string) =>
  store.dispatch(publish({ title, content }));
