import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { KeyValueObject } from 'src/utils/types';
import { AppDispatch } from 'src/@store';

export type AuthState = {
  pending: boolean;
  success?: boolean;
  error?: string;
  user?: any;
};

const initialState = {
  pending: false,
  user: null,
} as AuthState;

const auth = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    update(state, action: PayloadAction<KeyValueObject>) {
      state.user = action.payload;
    },
  },
});

export const authStateChange = (user: any) => async (dispatch: AppDispatch) => {
  dispatch(update(user));
};

const { actions, reducer } = auth;

export const { update } = actions;
export default reducer;
