import { QueryClauses } from './types';

export const toData = <T extends unknown>(
  docs: firebase.firestore.QueryDocumentSnapshot<firebase.firestore.DocumentData>[]
): T[] => {
  return docs.map((doc) => ({ ...doc.data(), id: doc.id } as T));
};

export const queryBuilder = async (
  builder: firebase.firestore.CollectionReference,
  clauses: QueryClauses
): Promise<firebase.firestore.Query> => {
  let cursor;
  let query = builder as firebase.firestore.Query;
  const { where = [], startAt, orderBy = [], limit = 10 } = clauses || {};

  if (startAt) {
    cursor = await builder.doc(startAt).get();
  }

  where.forEach(
    ([field, op, value]: any[]) => (query = query.where(field, op, value))
  );

  if (orderBy.length > 0) {
    const [field, type] = orderBy;
    if (field) {
      query = query.orderBy(field, type as any);
    }
  }

  if (cursor) {
    query = query.startAfter(cursor);
  }

  if (limit) query = query.limit(limit);

  return query;
};
