import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import * as fb from 'firebase/app';
import $APP from 'src/configs/app';
import { RootState } from 'src/@store';
import { Container } from 'semantic-ui-react';
import {
  pending,
  fulfilled,
  fulfilledAppend,
  rejected,
} from 'src/@slices/favourites/favouriteList';
import PageGuard from 'src/components/pageGuard';
import PageHeader from 'src/components/@partials/pageHeader';
import Gutter from 'src/theme/gutter';
import { KeyValueObject } from 'src/utils/types';
import { fetchMixes } from 'src/@creators/mixes';
import { dock } from 'src/utils/functors';
import { addSeries, mapGenres } from 'src/utils/relations';
import Playlist from 'src/components/playlist';
import image from 'src/assets/images/heart.svg';

const { PAGINATION } = $APP;
const docFieldName = fb.firestore.FieldPath.documentId();

const query = {
  orderBy: [docFieldName, 'asc'],
  limit: PAGINATION.limit,
} as KeyValueObject;

const base = (ids: string[] = []): any[] => [
  ['status', '==', 1],
  [docFieldName, 'in', ids],
];

const Favorites = () => {
  const {
    userFavourites: { ids = [], loaded },
    favouriteList: { mixes, loaded: mixesLoaded, lastPartCount },
    genres: { genres, loaded: genresLoaded },
    series: { series: seriesList, loaded: seriesLoaded },
  } = useSelector<RootState, RootState>((state) => state);

  const dispatch = useDispatch();
  const [limit] = useState($APP.PAGINATION.limit);
  const [playlistMixes, setPlaylistMixes] = useState([]);

  const fetch = () => {
    const offset = playlistMixes.length;
    const selectedIds = ids.slice(offset, offset + limit);
    if (selectedIds.length < 1) return;
    dispatch(
      fetchMixes(
        {
          where: [...base(selectedIds)],
          ...query,
        },
        pending,
        fulfilledAppend,
        rejected
      )
    );
  };

  // first fetch
  useEffect(() => {
    const selectedIds = ids.slice(0, limit);
    if (loaded) {
      dispatch(
        fetchMixes(
          {
            where: [...base(selectedIds)],
            ...query,
          },
          pending,
          fulfilled,
          rejected
        )
      );
    }
  }, [loaded, ids, limit, dispatch]);

  // load more
  useEffect(() => {
    if (mixesLoaded && seriesLoaded) {
      const series = dock(seriesList).map(mapGenres(genres)).get();
      const newMixes = dock(mixes).add(addSeries(series)).map(mapGenres(genres)).get();
      setPlaylistMixes(newMixes);
    }
  }, [mixes, mixesLoaded, seriesLoaded, genresLoaded, seriesList, dispatch, genres]);

  return (
    <PageGuard cond={true}>
      <Container fluid className="artistDetailPage reset">
        <PageHeader title="Favourites" image={image} />
        <Gutter
          top={2}
          right={1.5}
          bottom={2}
          left={1.5}
          style={{ overflowY: 'auto', height: 'calc(100vh - 230px)' }}
          key="list-gutter"
        >
          <Playlist
            items={playlistMixes}
            onLoad={
              ids.length > query.limit && lastPartCount === query.limit
                ? () => fetch()
                : undefined
            }
          />
        </Gutter>
      </Container>
    </PageGuard>
  );
};

export default Favorites;
