import React, { useState, useEffect } from 'react';
import { Grid, Header, Form, Button } from 'semantic-ui-react';
import Input from 'src/theme/input';
import { ReactInputEvent } from 'src/utils/types';
import { changePassword, ChangePasswordState } from 'src/@slices/account/changePassword';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'src/@store';
import { staticText } from 'src/utils/config';
import { success as successMessage, error as errorMessage } from 'src/@creators/toast';

const { CHANGE_PASSWORD } = staticText('ACCOUNT');

const ChangePassword = () => {
  const dispatch = useDispatch();
  const { error, pending, success } = useSelector<RootState, ChangePasswordState>(
    (state) => state.changePassword
  );
  const [newPassword, setNewPassword] = useState('');
  const onChange = ({ target }: ReactInputEvent) => {
    const { value } = target;
    setNewPassword(value);
  };
  const change = () => newPassword && dispatch(changePassword(newPassword));

  useEffect(() => {
    error && errorMessage(error);
    success && successMessage(CHANGE_PASSWORD.SUCCESS);
  }, [error, success]);
  return (
    <Grid className="change-password">
      <Grid.Row columns="3">
        <Grid.Column tablet="3" computer="3" largeScreen="4" widescreen="5" />
        <Grid.Column mobile="16" computer="10" tablet="10" largeScreen="8" widescreen="6">
          <Header as="h1">Change Password</Header>
          <Form autoComplete="off">
            <Form.Field>
              <Input
                type="password"
                autoComplete="off"
                name="email"
                size="large"
                fluid
                label="New Password:"
                value={newPassword}
                onChange={onChange}
                placeholder="New Password"
              />
            </Form.Field>
            <Button
              positive
              size="large"
              onClick={change}
              disabled={pending}
              content="SAVE"
            />
          </Form>
        </Grid.Column>
        <Grid.Column tablet="3" computer="3" largeScreen="4" widescreen="5" />
      </Grid.Row>
    </Grid>
  );
};

export default ChangePassword;
