import { useSelector, useDispatch } from 'react-redux';
import { RootState } from 'src/@store';
import { PrimaryId } from '../../utils/types';
import { addToUserFavourites, removeFromUserFavourites } from 'src/@creators/favorites';

const useFavorites = () => {
  const dispatch = useDispatch();
  const {
    auth: { user },
    userFavourites: { ids = [] },
  } = useSelector<RootState, RootState>((state) => state);

  const has = (id?: PrimaryId): boolean => id && user && ids.includes(id);
  const toggle = (id: PrimaryId, userId: PrimaryId = user && user.uid) =>
    has(id)
      ? dispatch(removeFromUserFavourites(userId, id))
      : dispatch(addToUserFavourites(userId, id));

  return {
    has,
    toggle,
  };
};

export default useFavorites;
