import React from 'react';
import { Icon } from 'semantic-ui-react';
import usePlayer from 'src/hooks/usePlayer';
import { Mix } from 'src/models/mix';

type Props = {
  mix: Mix;
};

const Play = ({ mix }: Props) => {
  const { play, currentMix, isPlaying, isLoading } = usePlayer();

  return (
    <div className="play-wrapper">
      {currentMix && currentMix.id === mix.id ? (
        <Icon
          link
          name={isPlaying ? 'pause' : isLoading ? 'sync alternate' : 'play'}
          loading={isLoading}
          disabled={isLoading}
          onClick={() => {
            play(mix);
          }}
        />
      ) : (
        <Icon
          link
          name={'play'}
          onClick={() => {
            play(mix);
          }}
        />
      )}
    </div>
  );
};

export default Play;
