import React, { useRef, useState, useEffect } from 'react';
import Slidify from '@atayahmet/react-slidify';
import { Grid, Responsive } from 'semantic-ui-react';
import usePlayer from 'src/hooks/usePlayer';
import TimelineCounter from '../../../timelineCounter';
import { calcUnit, ICalcUnit } from 'src/utils/functors';
import { IEventPoint } from '@atayahmet/react-slidify/lib/utils/interfaces';

const Timeline = () => {
  const {
    service: { audio },
    currentMix,
    isPlaying,
    hasError,
    seek,
    saveSeconds,
    getSeconds,
  } = usePlayer();

  const { currentTime } = audio;
  const [startTime, setStartTime] = useState(currentTime || getSeconds());
  const [x, setX] = useState(0);
  const containerRef = useRef<HTMLDivElement>(null);
  const durationRef = useRef<number>(currentMix?.duration || 0);
  const initialSecondsRef = useRef<number>(getSeconds());

  const onUpdate = (e?: any, data?: any) => {
    const el = containerRef.current || document.getElementById('tl-wrapper');
    el && setStartTime(audio.currentTime);
  };

  const onSeek = (e: IEventPoint) => {
    const { x } = e;
    const width = containerRef.current?.clientWidth || 0;
    const { current: duration = 0 } = durationRef;
    const elapsedSeconds = calcUnit({ left: x, right: width })
      .map(({ toPercent }: ICalcUnit) => calcUnit({ left: duration, right: toPercent() }))
      .toNumber();
    seek(elapsedSeconds);
    setStartTime(elapsedSeconds);
  };

  const onCount = (seconds: number) => {
    const containerWidth = containerRef?.current?.clientWidth || 0;
    setX(
      calcUnit({ left: seconds, right: durationRef.current })
        .map(({ toPercent }: ICalcUnit) => {
          return calcUnit({ left: containerWidth, right: toPercent() });
        })
        .toNumber()
    );
    saveSeconds(seconds);
  };

  // when current mix changed
  useEffect(() => {
    setX(0);
    setStartTime(0);
    durationRef.current = currentMix?.duration || 0;
  }, [currentMix]);

  // when any exception throwed
  useEffect(() => {
    if (hasError) {
      setX(0);
      setStartTime(0);
    }
  }, [hasError]);

  // set initial time
  useEffect(() => {
    const duration = durationRef.current;
    const seconds = initialSecondsRef.current;
    const containerWidth = containerRef?.current?.clientWidth || 0;
    
    setX(
      calcUnit({ left: seconds, right: duration })
        .map(({ toPercent }: ICalcUnit) => {
          return calcUnit({ left: containerWidth, right: toPercent() });
        })
        .toNumber()
    );
    setStartTime(seconds);
  }, []);

  return (
    <Grid className="timeline reset">
      <Grid.Row columns={2} verticalAlign="middle">
        <Grid.Column
          width="2"
          textAlign="right"
          className="reset-column duration"
          style={{ width: '50px!important' }}
        >
          <TimelineCounter
            current={startTime}
            duration={durationRef.current}
            active={isPlaying}
            onCount={onCount}
          />
        </Grid.Column>
        <Responsive fireOnMount as={Grid.Column} width={12} onUpdate={onUpdate}>
          <div ref={containerRef} id="tl-wrapper">
            <Slidify
              width={`${containerRef?.current?.clientWidth || '100%'}px`}
              height="15px"
              points={[
                {
                  x: x,
                  y: 0,
                  width: 12,
                  height: 12,
                  style: {
                    borderRadius: '7px',
                    marginTop: '1px',
                    backgroundColor: 'var(--high-text-color)',
                  },
                },
              ]}
              axis="x"
              onSlide={onSeek}
              movable
            />
          </div>
        </Responsive>
        <Grid.Column width="2" className="reset-column duration" textAlign="left">
          <TimelineCounter
            remaining
            current={startTime}
            duration={durationRef.current}
            active={isPlaying}
          />
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
};

export default Timeline;
