import { KeyValueObject } from './types';

type Add<T> = {
  items: T[];
  name: string;
  localId: string;
  foreignId: string;
};

type Map<T> = {
  items: T[];
  ids: string;
  localId: string;
};

export const dock = (data: any[]) => {
  return {
    add: ({ items, name, localId = '', foreignId }: Add<any>): any => {
      const result = data.map((d) => {
        return {
          ...d,
          [name]: items.find(({ [localId]: id }) => id === d[foreignId]),
        };
      });
      return dock(result);
    },
    map: ({ items, ids, localId }: Map<any>) => {
      const result = data.map((d) => {
        return {
          ...d,
          [ids]: (d[ids] || []).map((itemId: string) =>
            items.find(({ [localId]: id }) => itemId === id)
          ),
        };
      });
      return dock(result);
    },
    get: () => data,
  };
};

export type ICalcUnit = {
  left: (value: number) => number;
  right: (value: number) => number;
  toNumber: () => number;
  toPercent: () => number;
};

export const calcUnit = (data: KeyValueObject = {}) => {
  return {
    left: (value: number) => {
      return calcUnit({
        ...data,
        left: value,
      });
    },
    right: (value: number) => {
      return calcUnit({
        ...data,
        right: value,
      });
    },
    map: (cb: CallableFunction): ICalcUnit => {
      return cb(calcUnit(data));
    },
    toNumber: () => {
      const { left, right } = data;
      return (left / 100) * right;
    },
    toPercent: () => {
      const { left, right } = data;
      return (left / right) * 100;
    },
  };
};
