import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AppDispatch } from 'src/@store';
import Firebase, { FirebaseErrorProps } from 'src/services/firebase';

type RegisterState = {
  pending: boolean;
  success?: boolean;
  error?: string;
};

const initialState = {
  pending: false,
} as RegisterState;

const registerSlice = createSlice({
  name: 'register',
  initialState,
  reducers: {
    pending(state) {
      state.pending = true;
    },
    fulfilled(state) {
      state.pending = false;
      state.success = true;
    },
    rejected(state, action: PayloadAction<FirebaseErrorProps>) {
      const { message } = action.payload;
      state.error = message;
      state.pending = false;
      state.success = false;
    },
  },
});

export const register = (auth: any) => async (dispatch: AppDispatch) => {
  try {
    dispatch(pending());
    await Firebase.register(auth);
    dispatch(fulfilled())
  } catch ({ code, message }) {
    dispatch(rejected({ code, message }));
  }
};

const { actions, reducer } = registerSlice;

export const { pending, fulfilled, rejected } = actions;
export default reducer;
