import React, { useState, useEffect } from 'react';
import { Form, Header, Button, Grid } from 'semantic-ui-react';
import { ReactInputEvent } from 'src/utils/types';
import Avatar from 'src/components/avatar';
import Input from 'src/theme/input';
import { isUrl } from 'src/utils/generic';
import { ProfileState, update as updateProfile, } from 'src/@slices/account/profile';
import { useDispatch, useSelector } from 'react-redux';
import { ProfileModel } from 'src/models/profile';
import { RootState } from 'src/@store';
import { staticText } from 'src/utils/config';
import { success as successMessage, error as errorMessage } from 'src/@creators/toast';

type Props = {
  profile: ProfileModel;
};

type FormProps = {
  displayName: string;
  photoUrl: string;
};

const { PROFILE } = staticText('ACCOUNT');

const Profile = ({ profile }: Props) => {
  const dispatch = useDispatch();
  const { pending, error, success } = useSelector<RootState, ProfileState>(
    ({ profile }) => profile
  );
  const { displayName, photoUrl = '' } = profile;
  const [form, setForm] = useState({
    displayName,
    photoUrl,
  } as FormProps);

  const onChange = ({ target }: ReactInputEvent) => {
    const { name, value } = target;
    setForm({ ...form, [name]: value });
  };

  const update = () => {
    dispatch(updateProfile(form));
  };

  useEffect(() => {
    success && successMessage(PROFILE.SUCCESS);
    error && errorMessage(error);
  }, [success, error]);

  const { photoUrl: userPhotoUrl, displayName: userName } = form;
  const avatar = isUrl(userPhotoUrl) ? userPhotoUrl : userName;
  return (
    <Grid className="profile">
      <Grid.Row columns="3">
        <Grid.Column tablet="3" computer="3" largeScreen="4" widescreen="5" />
        <Grid.Column mobile="16" computer="10" tablet="10" largeScreen="8" widescreen="6">
          <Header as="h1">Profile</Header>
          <Form autoComplete="off">
            <Form.Field>
              <Avatar content={avatar} />
            </Form.Field>
            <Form.Field>
              <Input
                autoComplete="off"
                name="photoUrl"
                size="large"
                fluid
                label="Avatar Link:"
                value={userPhotoUrl}
                onChange={onChange}
                placeholder="Avatar Link"
                className={``}
              />
            </Form.Field>
            <Form.Field>
              <Input
                autoComplete="off"
                name="displayName"
                size="large"
                fluid
                label="Name:"
                value={userName}
                onChange={onChange}
                placeholder="Name"
              />
            </Form.Field>
            <Button
              positive
              size="large"
              onClick={update}
              disabled={pending}
              content="SAVE"
            />
          </Form>
        </Grid.Column>
        <Grid.Column tablet="3" computer="3" largeScreen="4" widescreen="5" />
      </Grid.Row>
    </Grid>
  );
};

export default Profile;
